import React, { useState } from 'react'

import Stars from '../Stars'

import { ReviewContainer, ReviewText, ReviewUser } from '../../styles/Info'
import { BtnContainer, EditBtn } from '../../styles/User'

function Review({ review, idx, admin, statusReview }) {

    const [seeReview, setSeeReview] = useState(false)

    const handleSeeReview = () => setSeeReview(!seeReview)

    const handleRejectReview = () => statusReview(review, false)
    const handleAcceptReview = () => statusReview(review, true)

    return (
        <ReviewContainer 
            bgColor={idx%2 === 0 ? 'rgb(246,246,246)' : 'rgb(235,235,235)'} 
            onClick={handleSeeReview}
            height={ (seeReview && review.texto) ? `${Math.ceil(review.texto.length/45)}vw` : '3vw' }
            mBottom={admin ? '1vw' : '0'}
        >
            <div className="review">
                <ReviewText>
                    <ReviewUser>{review.nombre?.toUpperCase()}: </ReviewUser>
                    {review.texto}
                </ReviewText>
                <Stars value={Math.floor(review.estrellas)}/>
            </div>
            {admin && (
                <BtnContainer justifyContent="center" margin="0 0 1vw 0">
                    <EditBtn onClick={handleRejectReview}>RECHAZAR</EditBtn>
                    <EditBtn onClick={handleAcceptReview}>ACEPTAR</EditBtn>
                </BtnContainer>
            )}
        </ReviewContainer>
    )
}

export default Review
