import React, { useRef, useState } from 'react'
import Modal from 'react-modal';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes, faCamera } from "@fortawesome/free-solid-svg-icons";
import Loader from 'react-loader-spinner'

import { ModalImagesContainer, AddImageBtn, BtnModalContainer } from '../../styles/Admin'
import { ContentContainer, TitleContainer } from '../../styles/Date'
import { EditBtn, Message } from '../../styles/User'

Modal.setAppElement('#root')

const customStyles = {
    content : {
        top: '20%',
        left: '20%',
        right: 'auto',
        bottom: 'auto',
        width: '60%',
        height: '60%',
        borderRadius: '0',
        border: '0',
    }
};

const ImgItem = ({ img, handleItem, idx }) => {

    const handleBtn = () => handleItem(idx)

    return(
        <div>
            <img src={img.url || img}/>
            <button onClick={handleBtn}>
                <FontAwesomeIcon icon={faTimes} color="rgb(0,47,91)"  />
            </button>
        </div>
    )
}


function ModalEdit({ modal, closeModal, imgs, removeItem, addImage, saveImages, deletedImgs, uploadImgs }) {

    const refInputFile = useRef(null)

    const [err, setErr] = useState(false)
    const [loading, setLoading] = useState(false)
    const [message, setMessage] = useState('')

    const handleAddImage = () => refInputFile.current.click()

    const handleInputImage = e => { 
        if(e.target.files[0].size > 5000000){
            setErr(true)
            return setMessage('LA IMAGEN DEBE PESAR MENOS DE 5MB')
        }
        addImage(e.target.files[0])
    }

    const getFormData = () => {
        let formData = new FormData();
        uploadImgs.forEach(img => {
            if(!img.id){ formData.append('images', img.img) }
        })
        formData.append('deletedImgs', JSON.stringify(deletedImgs))
        return formData;
    }

    const checkImgs = () => {
        if(imgs.length === 0){
            setErr(true)
            setMessage('No puede dejar sin imagenes al carrusel.')
            return false
        }
        return true
    }

    const handleBtn = () => {
        if(checkImgs() && !loading){
            setErr(false)
            setLoading(true)
            saveImages(getFormData()).then(() => {
                window.location.reload()
            }).catch(e => {
                console.log(e)
                setLoading(false)
                setErr(true)
                if(e.response?.status === 401){ return setMessage('Su sesión ha expirado. Vuelva a iniciar sesión.') }
                setMessage('Ha ocurrido un error intentando actualizar las imágenes. Itentalo más tarde o reporta el problema.')
            })
        }
    }

    return (
        <Modal
            isOpen={modal}
            onRequestClose={closeModal}
            style={customStyles}
            contentLabel="Example Modal"
        >
            <ContentContainer pTop="0">
                <TitleContainer width="90%"><h1>IMÁGENES</h1></TitleContainer>
            </ContentContainer>
            <ModalImagesContainer>
                {imgs.map( (img, idx) => (
                    <ImgItem 
                        img={img}
                        key={idx}
                        idx={idx}
                        handleItem={removeItem}
                    />
                ))}
                <AddImageBtn onClick={handleAddImage}>
                    <FontAwesomeIcon icon={faCamera} color="rgb(100,100,100)"  />
                </AddImageBtn>
            </ModalImagesContainer>
            <input 
                style={{ display: 'none' }}
                type="file"
                ref={refInputFile}
                accept="image/*"
                name="image"
                onChange={handleInputImage}
            />
            <BtnModalContainer>
                    <EditBtn margin=" 1vw 0" onClick={handleBtn}>GUARDAR</EditBtn>
                    {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
            </BtnModalContainer>
            {err && (
                <BtnModalContainer>
                    <Message mTop="0">{message}</Message>
                </BtnModalContainer>
            )}
        </Modal>
    )
}

export default ModalEdit
