import styled from 'styled-components'

const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightBlue = "rgb(178,223,252)"

export const ScheduleContainer = styled.div`
    width: 100%;
    max-height: 52vh;
    border: 2px solid rgb(240, 240, 240);
    overflow-y: auto;
    overflow-x: hidden;
    flex-shrink: 0;
    ::-webkit-scrollbar { width: 10px; }
    ::-webkit-scrollbar-track { background: #f1f1f1; }
    ::-webkit-scrollbar-thumb { background: #888; }
    ::-webkit-scrollbar-thumb:hover { background: #555; }
`

export const ScheduleTitleContainer = styled.div`
    width: 100%;
    padding: 1vw 0;
    display: flex;
    flex-direction: row;
    align-items: center;

    h3{
        font-family: Montserrat;
        font-size: 2.5vw;
        margin: 0 auto;
    }
`

export const ScheduleDay = styled.div`
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-family: MontserratLight;
        font-size: 1vw;
        margin: 0;
    }

    h2{
        font-family: Montserrat;
        font-size: 3vw;
        margin: 0;
        margin-top: 0.2vw;
    }

`

export const ScheduleContent = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 1vw;
`
export const ScheduleHour = styled.div`
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: ${props => props.alignItems};
    padding-left: 3%;
    padding-right: 5%;
    justify-content: space-between;
    min-height: 2vw;

    span{
        font-family: Montserrat;
        font-size: 0.8vw;
        margin-right: 1vw;
        margin-top: ${props => props.mTop};
        width: 10%;
        text-align: center;
        background-color: ${props => props.bColor};
        color: ${props => props.bColor === "white" ? 'black' : 'white'};
        border-radius: 0.2rem;

        &:hover{
            cursor: ${props => props.cursor};
        }
    }
`

export const ScheduleDateContainer = styled.div`
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-left: 0.2vw solid rgb(240, 240, 240);
    position: relative;
    z-index: 1;

    .line{
        width: 100%;
        height: 0;
        border: 0.1vw solid ${props => props.bColor};
        background-color: ${props => props.bColor};
        position: relative;
        bottom: -0.9vw;
        left: -0.4vw;
        z-index: 0;
    }

    .content{
        height: ${props => props.height};
        width: 100%;
        flex-shrink: 0;
        font-family: Montserrat;
        font-size: 1vw;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        padding-top: 0.3vw;
        position: relative;
        bottom: -0.9vw;
        padding-left: 1vw;
        z-index: 2;
        transition: height 0.5s ease;
        overflow-y: hidden;

        p{
            width: 100%;
            margin: 0;
        }

        .see-more{
            color: ${darkBlue};
            &:hover{ cursor: pointer; }
            margin-top: 0;
            background-color: white;
            width: fit-content;
        }

        p:first-child{ margin-top: 0.6vw; }
    }
`

export const ScheduleCont = styled.div`
    width: 40%;
    display: flex;
    flex-direction: column;
`

export const ScheduleLabelsContainer = styled.div`
    width: 100%;
    margin: 0.5vw 0;
    padding: 0.5vw 0;
    border: 0.2vw solid rgb(240, 240, 240);
    display: flex; 
    align-items: center;
    justify-content: center;

    div{
        width: 1vw;
        height: 1vw;
        border-radius: 0.5vw;
        margin: 0 1vw;
    }

    span{ font-family: Montserrat; }

    .blue{ background-color: ${darkBlue} }
    .purple{ background-color: ${purple} }
`

export const PatientItemContainer = styled.div`
    min-height: 3vw;
    width: 95%;
    margin: 0 2.5%;
    padding: 0 1vw;
    background-color: ${ props => props.bgColor };
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    div:first-child{ min-width: 30%; }

    div{
        display: flex;
        flex-direction: row;
        align-items: center;

        img{
            width: 1.5vw;
            height: 1.5vw;
            border-radius: 0.2rem;
        }

        span{
            font-family: Montserrat;
            font-size: 1vw;
            margin-left: 1vw;
        }

        .image-default{
            width: 1.5vw;
            height: 1.5vw;
            border-radius: 0.2rem;
            background-color: rgb(240,240,240);
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0;
            border: 1px solid ${darkBlue};
            min-width: fit-content;
        }

        button{
            background-color: ${darkBlue};
            font-family: MontserratMedium;
            font-size: 0.7vw;
            color: white;
            padding: 0 1vw;
            border-radius: 0.3rem;
            border: 0;
            margin-left: 1vw;
            height: 1.5vw;

            &:hover{ cursor: pointer; }
        }
    }

    .service-name{ width: 30%; }

    .warning-text{
        width: 100%;
        display: flex;
        justify-content: center;

        span{ margin: 0; }
    }

    .see-more{
        font-family:Montserrat;
        font-size: 1vw;
        text-decoration: underline;
        color: ${darkBlue};

        &:hover{ cursor: pointer; }
    }
`

export const FilterContainer = styled.div`
    width: 80vw;
    margin: 0 10vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    .select-container{
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 25%;

        label{
            font-family: Montserrat;
            font-size: 1vw;
            margin-right: 1vw;
        }
    }
`

export const FilterDateSearch = styled.form`
    display: flex;
    flex-direction: row;
    align-items: center;

    label, span{
        font-family: Montserrat;
        font-size: 1vw;
        margin: 0 0.5vw;
    }

    input{
        border: 0;
        font-family: Montserrat;
        width: 3vw;
        height: 1.5vw;
        text-align: center;
        border-radius: 0.3rem;
        background-color: rgb(240, 240, 240);
        
        &:focus{ outline: 0; }
    }

    button{
        background-color: ${darkBlue};
        font-family: MontserratMedium;
        font-size: 0.7vw;
        color: white;
        padding: 0 1vw;
        border-radius: 0.3rem;
        border: 0;
        margin-left: 1vw;
        height: 1.5vw;

        &:hover{ cursor: pointer; }
    }

    .input-patient{
        width: 15vw;
        text-align: left;
        padding-left: 1vw;
    }
`

export const ScheduleTableContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const ScheduleTable = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    max-height: 60vh;
    margin-bottom: 1vw;
`

export const ScheduleDayContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;

    div{
        width: ${props => props.width || '11vw'};
        height: 5vh;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 0.1vw;
        flex-shrink: 0;
    }
    
    .header{
        background-color: ${darkBlue};
        font-family: MontserratBold;
        color: white;
        font-size: 1vw;
    }

    .row{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        background-color: ${props => props.bgColor || 'white'};

        .picker{
            width: fit-content;
            input{ 
                width: 4vw;
                font-size: 1vw 
            }
        }

        span{
            margin: 0 0.1vw; 
            font-family: Montserrat;
            font-size: 0.8vw;
        }

        .span-to-show{ margin: 0 }
    }
`
