import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import Input from './Input'
import TokenExpirate from '../TokenExpirate'

import { ContainerForm, InfoContainer, BtnCircle, EditBtn, Message, LoadingContainer } from '../../styles/User'

import { URL } from '../../variables'

function Form({ id, recipe }) {

    const [inputs, setInputs] = useState([''])
    const [err, seteErr] = useState(false)
    const [loading, setLoading] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [success, setSuccess] = useState(false)
    const [messageErr, setMessageErr] = useState('')

    const addInput = (e) => {
        e.preventDefault()
        let newInputs = [...inputs]
        newInputs.push('')
        setInputs(newInputs)
    }

    const removeInput = (idx) => {
        console.log(idx)
        let newInputs = [...inputs]
        newInputs.splice(idx, 1)
        setInputs(newInputs)
    }

    const handleInput = (idx, value) => {
        let newInputs = [...inputs]
        newInputs.splice(idx, 1, value)
        setInputs(newInputs)
    }

    const saveRecipe = async () => {
        const res = await axios({
            url: `${URL}/cita/recipe`,
            method: 'POST',
            data: { inputs, id }
        })
        return res.data
    }

    const checkData = () => {
        if(inputs[0] === ""){
            seteErr(true)
            setMessageErr('Al menos tiene que llenar un campo')
            return false
        }
        return true
    }

    const handleBtn = e => {
        e.preventDefault()
        if(checkData()){
            setLoading(true)
            seteErr(false)
            saveRecipe().then(() => {
                setLoading(false)
                setSuccess(true)
            }).catch(e => {
                setLoading(false)
                console.log(e)
                if(e.response.status === 401){ return setTokenExpirate(true) }
                seteErr(true)
                setMessageErr('Ha ocurrido un error en el servidor. Intentalo más tarde o reporta el problema.')
            })
        }
    }

    useEffect(() => {
        if(recipe.length !== 0){
            const recipes = recipe.map(recipe => recipe.receta) 
            setInputs(recipes)
            setSuccess(true)
        }
    }, [recipe])

    return (
        <ContainerForm>
            <InfoContainer width="100%">
                {inputs.map( (input, idx) => (
                    idx === 0 ? (
                        <Input 
                            idx={idx+1} 
                            removeInput={removeInput} 
                            key={idx}
                            value={input}
                            onChange={handleInput}
                            hasRecipe={success}
                        >
                            {!success && <BtnCircle onClick={addInput}>+</BtnCircle>}
                        </Input>
                    ) : (
                        <Input 
                            idx={idx+1} 
                            removeInput={removeInput} 
                            key={idx}
                            value={input}
                            onChange={handleInput}
                            hasRecipe={success}
                        />
                    )
                ) )}
                <LoadingContainer>
                    {err && (<Message>{messageErr}</Message>)}
                    {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
                </LoadingContainer>
                {!success && <EditBtn onClick={handleBtn} margin="0">GUARDAR RECETA</EditBtn>}
            </InfoContainer>
            <TokenExpirate modal={tokenExpirate} admin/>
        </ContainerForm>
    )
}

export default Form
