import React, { useState, useEffect } from 'react'
import axios from 'axios'

import Form from './Form'
import TokenExpirate from '../TokenExpirate'

import { ContentContainer, TitleContainer, ErrMessage } from '../../styles/Date'

import { URL } from '../../variables'

function View() {

    const [name, setName] = useState(null)
    const [type, setType] = useState(null)
    const [description, setDescription] = useState(null)
    const [image, setImage] = useState(null)
    const [facebook, setFacebook] = useState(null)
    const [instagram, setInstagram] = useState(null)
    const [phone, setPhone] = useState(null)
    const [message, setMessage] = useState('')
    const [sessionExpirate, setSessionExpirate] = useState(false)
    const [err, setErr] = useState(false)

    const getInfo = async () => {
        const res = await axios({
            url: `${URL}/admin/user`,
            method:'GET'
        })

        return res.data
    }

    useEffect(() => {
        getInfo().then(({ user }) => {
            setName(user.nombre)
            setType(user.cargo)
            setDescription(user.descripcion)
            setImage(user.imagen)
            setFacebook(user.facebook)
            setInstagram(user.instagram)
            setPhone(user.telefono)
        }).catch(e => {
            console.log(e)
            if(e.reponse?.status === 401){ return setSessionExpirate(true) }
            setErr(true)
            setMessage('No ha sido posible obtener la información debido a un error en el servidor. Intentalo más tarde o reporta el problema.')
        })
    }, [])

    return (
        <ContentContainer pTop="0">
            <TitleContainer><h1>EDITAR</h1></TitleContainer>
            <Form 
                prevName={name}
                prevType={type}
                prevDescription={description}
                prevImage={image}
                prevFacebook={facebook}
                prevInstagram={instagram}
                prevPhone={phone}
                setErr={setErr}
                setMessage={setMessage}
                setSessionExpirate={setSessionExpirate}
            />
            {err && <ErrMessage color="red" width="100%">{message}</ErrMessage>}
            <TokenExpirate modal={sessionExpirate} admin/>
        </ContentContainer>
    )
}

export default View
