import styled from 'styled-components'

const darkBlue = "rgb(0,47,91)"
const lightGray = "rgb(235,235,235)"

export const FormContainer = styled.form`
    width: 75%;
    margin: 0 12.5%;
    display: flex;
    flex-direction: column;
    align-items: center;

    button{
        font-family: MontserratBold;
        color: white;
        font-size: 1vw;
        padding: 0 1vw;
        background-color: ${darkBlue};
        border-radius: 0.3rem;
        border: 0;
        height: fit-content;
        width: fit-content;
        margin: 0;
        &:hover{ cursor: pointer; }
    }

    .textarea-container{ align-items: flex-start; }

    div{
        display: flex;
        flex-direction: row;
        width: 70%;
        align-items: center;
        margin-bottom: 1vw;

        input{
            font-family: Montserrat;
            background-color: ${lightGray};
            font-size: 1vw;
            padding: 0.5vw 1vw;
            border: 0;
            border-radius: 0.5rem;
            width: 100%;
            &:focus{ outline: 0; }
        }

        textarea{
            font-family: Montserrat;
            background-color: ${lightGray};
            font-size: 1vw;
            padding: 0.5vw 1vw;
            border: 0;
            border-radius: 0.5rem;
            width: 100%;
            resize: none;
            height: 5vw;
            &:focus{ outline: 0; }
        }
    }
`

export const Label = styled.label`
    font-family: Montserrat;
    font-size: 1vw;
    width: ${props => props.width || '40%'};
`