import React from 'react'
import Modal from 'react-modal';

import { TokenExpirateModal } from '../../styles/Admin' 

Modal.setAppElement('#root')

const customStyles = {
    content : {
        top: '35%',
        left: '40%',
        right: 'auto',
        bottom: 'auto',
        width: '20%',
        height: '30%',
        borderRadius: '0',
        border: '0',
    }
};

function ModalRejectDate({ modal, handleBtn, closeModal }) {
    return (
        <Modal
            isOpen={modal}
            onRequestClose={closeModal}
            style={customStyles}
            contentLabel="Example Modal"
        >
            <TokenExpirateModal>
                <h1>¿Está seguro que desea rechazar esta cita?</h1>
                <button onClick={handleBtn}>RECHAZAR</button>
            </TokenExpirateModal>
        </Modal>
    )
}

export default ModalRejectDate
