import React from 'react'
import { faFacebook, faInstagram } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import Day from '../schedule/Day'

import { InfoItemContainer, InfoTitleContainer, InfoTitle, InfoContent, InfoImageContainer, InfoImage, InfoSocialMediaContainer, InfoBtn,
        InfoDescriptionContainer, InfoName, InfoType, InfoDescription, LabImageContainer } from '../../styles/Info'

import { ReactComponent as HemIcon } from '../../assets/icons/hematologia.svg';
import { ReactComponent as CoaIcon } from '../../assets/icons/coagulacion.svg';
import { ReactComponent as QuSaIcon } from '../../assets/icons/quimica-sanguinea.svg';

function InfoContainer({ info, children, infoType, monday, tuesday, wednesday, thursday, friday, saturday, sunday }) {

    const days = ['Lun', 'Mar', 'Mié', 'Jue', 'Vie', 'Sab', 'Dom']

    const getInfoImage = () => {
        if(infoType === "laboratorio"){
            switch(info.laboratorio){
                case "HEMATOLOGÍA": return( <HemIcon /> )
                case "COAGULACIÓN": return( <CoaIcon /> )
                case "QUÍMICA SANGUINEA": return( <QuSaIcon /> )
                default:
                    break;
            }
        }

        return ( <InfoImage src={info.imagen}/> )
    }

    const getDayValues = (day) => {
        if(day === 'Lun'){ return monday }
        else if(day === 'Mar'){ return tuesday }
        else if(day === 'Mié'){ return wednesday }
        else if(day === 'Jue'){ return thursday }
        else if(day === 'Vie'){ return friday }
        else if(day === 'Sab'){ return saturday }
        else{ return sunday }
    }

    const gotoDate = () => window.location.replace(`/cita?id=${info.id}&type=${infoType}`)    

    const gotoFacebook = () => window.open(info.facebook)
    const gotoInstagram = () => window.open(info.instagram)

    return (
        <InfoItemContainer> 
            <InfoTitleContainer>
            <InfoTitle>{infoType === "medico" ? 'PERFIL' : 'LABORATORIO'}</InfoTitle>
            </InfoTitleContainer>
            <InfoContent>
                <InfoImageContainer>
                    <LabImageContainer>
                        {getInfoImage()}
                    </LabImageContainer>
                    <InfoSocialMediaContainer>
                        {info.facebook && <FontAwesomeIcon icon={faFacebook} color="rgb(0,47,91)" size="2x" onClick={gotoFacebook}/>}
                        {info.instagram && (
                            <FontAwesomeIcon 
                                icon={faInstagram} 
                                color="rgb(0,47,91)" 
                                size="2x" 
                                style={{ marginLeft: info.facebook ? '1vw' : '0' }} 
                                onClick={gotoInstagram}
                            />
                        )}
                    </InfoSocialMediaContainer>
                    {info.telefono && <InfoDescription margin="0">Tel. {info.telefono}</InfoDescription>}
                    <InfoBtn onClick={gotoDate}>HAZ TU CITA</InfoBtn>
                    <InfoDescription margin="0">Precio: ${info.precio}</InfoDescription>
                </InfoImageContainer>
                <InfoDescriptionContainer>
                    {infoType === "medico" ? (
                        <>
                        <InfoName>{info.nombre}</InfoName>
                        <InfoType>{info.cargo?.toUpperCase()}</InfoType>
                        </>
                    ):(
                        <>
                        <InfoName>{info.laboratorio}</InfoName>
                        <InfoType>{info.nombre}</InfoType>
                        </>
                    )}
                    <InfoDescription>{info.descripcion}</InfoDescription>
                    <InfoName>Horario de atención</InfoName>
                    <div className="calendar">
                        {days.map( (day, idx) => (
                            <Day 
                                key={day} 
                                day={day} 
                                values={getDayValues(day)}
                                toShow
                                width="5.5vw"
                                idx={idx}
                            />
                        ))}
                    </div>
                    
                </InfoDescriptionContainer>
                {children}
            </InfoContent>
        </InfoItemContainer>
    )
}

export default InfoContainer
