import styled from 'styled-components'

const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightBlue = "rgb(178,223,252)"

export const Container = styled.div`
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
`

export const ContainerForm = styled.form`
    width: ${props => props.width || '74.5%'};
    display: flex;
    flex-direction: row;
    justify-content: space-between;
`

export const DefaultImage = styled.div`
    width: ${props => props.size || '15vw'};;
    height: ${props => props.size || '15vw'};;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(240,240,240);
    border: 1px solid ${darkBlue};
    margin-bottom: 1vw;
    flex-shrink: 0;
`

export const Image = styled.img`
    width: ${props => props.size || '15vw'};
    height: ${props => props.size || '15vw'};
    border-radius: 0.5rem;
    border: 1px solid ${darkBlue};
    object-fit: cover;
`

export const InputImage = styled.div`
    width: 15vw;
    height: 15vw;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(240,240,240);
    border: 1px solid ${darkBlue};
    &:hover{ cursor: pointer; }
`

export const InfoContainer = styled.div`
    width: ${props => props.width || '50%'};
    height: ${props => props.height || 'fit-content'};
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
`

export const InfoItem = styled.div`
    display: flex;
    align-items: ${props => props.alignItems || 'flex-start'};
    width: 100%;
    flex-direction: row;
    justify-content: ${props => props.justifyContent || 'space-between'};
    margin-bottom: ${props => props.mgBottom || '1vw'};
    margin-left: ${props => props.itemMLeft || '0'};

    span{
        font-family: MontserratMedium;
        color: ${darkBlue};
        font-size: ${props => props.fontSize || '1.5vw'};
        width: ${props => props.labelWidth || 'fit-content'};
    }

    label{
        font-family: MontserratMedium;
        color: ${darkBlue};
        font-size: ${props => props.fontSize || '1.5vw'};
        width: ${props => props.labelWidth || 'fit-content'};
    }

    p{
        font-family: Montserrat;
        font-size: ${props => props.fontSize || '1.5vw'};;
        margin: 0;
        text-align: right;
    }

    input{
        width: 60%;
        font-family: Montserrat;
        font-size: 1vw;
        padding: 0.5vw 1vw;
        background-color: rgb(240,240,240);
        border-radius: 0.5rem;
        border: 0;
        height: 1.5vw;
        margin: ${props => props.mInput || '0'};
        &:focus{ outline: 0; }
    }

    .input-container{
        width: 60%;
        background-color: rgb(240,240,240);
        padding: 0.5vw 1vw;
        height: 1.5vw;
        margin: ${props => props.mInput || '0'};
        border-radius: 0.5rem;
        flex-direction: row;
        display: flex;
        align-items: center;
        justify-content: space-between;

        input{
            width: 90%;
            padding: 0;
            margin: 0;

        }

        button{
            opacity: 0;
            transition: opacity 0.25s ease-in;
            color: ${darkBlue};
            border: 0;
            font-size: 1vw;
            &:hover{ 
                opacity: 1; 
                cursor: pointer;
            }
        }
    }   
`

export const EditBtn = styled.button`
    font-family: MontserratBold;
    color: white;
    font-size: 1vw;
    padding: 0.5vw 1vw;
    background-color: ${darkBlue};
    border-radius: 0.3rem;
    border: 0;
    height: fit-content;
    width: fit-content;
    margin: ${props => props.margin || '0 0 0 1vw'};
    &:hover{ cursor: pointer; }
`

export const BtnContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: ${props => props.justifyContent || 'flex-end'};
    align-items: center;
    margin: ${props => props.margin || '0'};
    button:first-child{ margin-right: 1vw; }
`

export const Message = styled.span`
    color: ${props => props.color || 'red'};
    font-family: MontserratBold;
    font-size: 1.5vw;
    width: 100%;
    text-align: center;
    margin-top: ${props => props.mTop || '2vw'};
`

export const LoaderContainer = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center !important;
`

export const MenuContainer = styled.div`
    width: 20%;
    height: fit-content;
    background-color: ${lightBlue};
    display: flex;
    flex-direction: column;
    border-radius: 0.5rem;
    flex-shrink: 0;

    h2{
        background-color: ${darkBlue};
        width: 100%;
        margin: 0;
        text-align: center;
        border-radius: 0.5rem;
        padding: 1vw 0;
        font-family: Montserrat;
        color: white;
        font-size: 2vw;
    }

    ul{
        display: flex;
        flex-direction: column;
        padding: 0 3vw;
        list-style: none;

        li:before { 
            content: "";
            border-color: transparent rgb(71, 112, 144);;
            border-style: solid;
            border-width: 0.35em 0 0.35em 0.45em;
            display: block;
            height: 0;
            width: 0;
            left: -1em;
            top: 0.9em;
            position: relative;
        }

        li{
            font-family: MontserratMedium;
            font-size: 1.3vw;
            color: rgb(71, 112, 144);
            &:hover{ cursor: pointer; }
        }
    }
`

export const BtnSeeMoreDate = styled.button`
    width: fit-content;
    padding: 0.5vw 1vw;
    border-radius: 0.5rem;
    font-family: MontserratMedium;
    color: white;
    background-color: ${darkBlue};
    border: 0;
    margin-left: 1vw;

    &:hover{ cursor: pointer; }
`

export const HistoryContainer = styled.div`
    width: 75%;
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const DateItemContainer = styled.div`
    width: 100%;
    height :${props => props.height};
    transition: height 0.5s ease;
    display: flex;
    flex-direction: row;
    padding: 0.5vw 1vw;
    background-color: ${props => props.bgColor};
    margin-bottom: 0.5vw;
    overflow-y: hidden;

    .column{
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &:hover{ cursor: pointer; }
`

export const DateItemInfo = styled.div`
    display: flex;
    flex-direction: row;
    width: ${props => props.width};
    margin-top: ${props => props.mTop || '0'};
    justify-content: ${props => props.justifyContent || 'flex-start'};
    align-items: ${props => props.alignItems || 'flex-start'};
    flex-shrink: 0;
    overflow: hidden;

    span{
        font-family: Montserrat;
        font-size: 1vw;
    }

    .bold{ font-family: MontserratBold }
`

export const ImageContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const BtnCircle = styled.button`
    width: 2vw;
    height: 2vw;
    border-radius: 1vw;
    font-family: MontserratMedium;
    color: white;
    background-color: ${darkBlue};
    border: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: ${props => props.mLeft || '1vw'};
    font-size: 1.2vw;

    &:hover{ cursor: pointer; }
    &:focus{ outline: 0; }
`

export const LoadingContainer = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1vw;
`