import React, { useState } from 'react'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";

import Stars from '../Stars'

import { DoctorItemContainer, DoctorImage, DoctorInfo, DoctorName, DoctorType, DoctorStars, DoctorDescription, DoctorLink, DoctorBtnDelete } from '../../styles/Doctors'
import { DefaultImage } from '../../styles/User'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario_imagen_default.svg';

function DoctorItem({ doctor, onClick, woDescription, itemWidth, doctorSelected, admin, deleteItem }) {

    const [width, setWidth] = useState('0')

    const onHover = () => setWidth('2vw')
    const onLeave = () => setWidth('0')

    const handleBtn = () => onClick(doctor) 

    const handleDeleteBtn = () => deleteItem(doctor)

    return (
        <DoctorItemContainer 
            onClick={handleBtn} 
            width={`${itemWidth}vw`}
            fillBg={doctorSelected?.id === doctor.id}
            onMouseEnter={onHover}
            onMouseLeave={onLeave}
        >
            {(doctor.imagen || doctor.usaimagen) ? <DoctorImage src={doctor.usaimagen || doctor.imagen}/>:(
                <DefaultImage size="7vw">
                    <UserIcon width="5vw" height="5vw"/>
                </DefaultImage>
            )}
            
            <DoctorInfo>
                <DoctorName>{doctor.nombre || doctor.usanombre}</DoctorName>
                <DoctorType>{doctor.cargo?.toUpperCase() || doctor.usacargo.toUpperCase()}</DoctorType>
                {!admin && (
                    <DoctorStars>
                        <Stars value={Math.floor(doctor.estrellas)}/>
                    </DoctorStars>
                )}
                {(!woDescription && !admin) && (
                    doctor.descripcion?.length <= 100 ? (
                        <DoctorDescription>{doctor.descripcion}</DoctorDescription>
                    ):(
                        <div>
                            <DoctorDescription>
                                {doctor.descripcion?.slice(0,85)}
                                <DoctorLink>VER MÁS...</DoctorLink>
                            </DoctorDescription>
                            
                        </div>
                    )
                )}
                {admin && <DoctorDescription>{doctor.email}</DoctorDescription>}
            </DoctorInfo>
            {admin && ( 
                <DoctorBtnDelete onClick={handleDeleteBtn} width={width}>
                    <FontAwesomeIcon icon={faTimes} color="white" />
                </DoctorBtnDelete>
            )}
        </DoctorItemContainer>
    )
}

export default DoctorItem
