import React from 'react'

import { ScheduleContainer, ScheduleTitleContainer, ScheduleDay, ScheduleContent, ScheduleCont, ScheduleLabelsContainer } from '../../styles/Doctor'

import HourItem from './HourItem'

function Schedule({ date, dates, seeInfo }) {

    const days = ['DOM', 'LUN', 'MAR', 'MIÉ', 'JUE', 'VIE', 'SAB']
    const month = ['ENERO', 'FEBRERO', 'MARZO', 'MAYO', 'ABRIL', 'JUNIO', 'JULIO', 'AGOSTO', 'SEPTIEMBRE', 'OCTUBRE', 'NOVIEMBRE', 'DICIEMBRE']
    const hours = ['00:30', '01:00', '01:30', '02:00', '02:30', '03:00', '03:30', '04:00', '04:30', '05:00', '05:30', '06:00', '06:30', 
                    '07:00', '07:30', '08:00', '08:30', '09:00', '09:30', '10:00', '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30',
                    '14:00', '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30', '19:00', '19:30', '20:00', '20:30',
                    '21:00', '21:30', '22:00', '22:30', '23:00', '23:30']

    const getMonth = () => `${month[new Date(date).getMonth()]} ${new Date(date).getFullYear()}`

    const getDay = () => new Date(date).getDate()

    const getDayName = () => days[new Date(date).getDay()]

    return (
        <ScheduleCont>
            <ScheduleContainer>
                <ScheduleTitleContainer>
                    <ScheduleDay>
                        <h1>{getDayName()}</h1>
                        <h2>{getDay()}</h2>
                    </ScheduleDay>
                    <h3>{getMonth()}</h3>
                </ScheduleTitleContainer>
                <ScheduleContent>
                    {hours.map( hour => (
                        <HourItem 
                            hour={hour}
                            key={hour}
                            dates={dates}
                            seeInfo={seeInfo}
                        />
                    ))}
                </ScheduleContent>
            </ScheduleContainer>
            <ScheduleLabelsContainer>
                <div className="blue"/>
                <span>Consultorio</span>
                <div className="purple"/>
                <span>Domicilio</span>
            </ScheduleLabelsContainer>
        </ScheduleCont>
    )
}

export default Schedule
