import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import TokenExpirate from '../TokenExpirate'
import ReviewsGroup from './ReviewsGroup'

import { Content, MessageError, EmptyWarning } from '../../styles/Admin'

import { URL } from '../../variables'

function Reviews({ scrollToBottom }) {

    const [reviews, setReviews] = useState([])
    const [reviewsGroup, setReviewsGroup] = useState([])
    const [err, setErr] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [loading, setLoading] = useState(true)
    const [getReviewsControl, setGetReviewsControl] = useState(true)
    const [message, setMessage] = useState('')

    const getReviews = async () => {
        const res = await axios({
            url: `${URL}/admin/reviews?start=${reviews.length}`,
            method: 'GET'
        })

        return res.data
    }

    const acceptReviews = async data => {
        const res = await axios({
            url: `${URL}/admin/reviews`,
            method: 'PUT',
            data
        })

        return res.data
    }

    const sortReviews = reviews => {
        let newReviewsGroup = [...reviewsGroup]
        let newReviews = [...reviews, ...reviews]

        reviews.forEach(review => {
            const idx = newReviewsGroup.findIndex( group => group.medico === review.medico || group.servicio === review.servicio )
            if(idx < 0){
                if(review.medico){
                    newReviewsGroup.push({
                        medico: review.medico,
                        title: review.mednombre,
                        reviews: [review]
                    })
                }else{
                    newReviewsGroup.push({
                        servicio: review.servicio,
                        title: review.servnombre,
                        reviews: [review]
                    })
                }
            }else{
                const lastReviews = newReviewsGroup[idx].reviews
                newReviewsGroup[idx].reviews = [...lastReviews, review]
            }
        })

        setReviews(newReviews)
        setReviewsGroup(newReviewsGroup)
    }

    const removeReview = (review) => {
        let newReviewsGroup = [...reviewsGroup]
        const idx = newReviewsGroup.findIndex( group => group.medico === review.medico || group.servicio === review.servicio )
        const idxReview = newReviewsGroup[idx].reviews.findIndex( review => review.id === review.id )
        let lastReviews = [...newReviewsGroup[idx].reviews]
        lastReviews.splice(idxReview, 1)
        newReviewsGroup[idx].reviews = lastReviews
        setReviewsGroup(newReviewsGroup)
    }

    const statusReview = (review, accept) => {
        setLoading(true)
        setErr(false)
        acceptReviews({ id: review.id, accept }).then(() => {
            setLoading(false)
            removeReview(review)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response?.status === 401){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido un error intentado actualizar esta reseña. Intentalo más tarde o reporta el problema.')
        })
    }

    useEffect(() => {
        getReviews().then(({ reviews }) => {
            setLoading(false)
            sortReviews(reviews)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response?.status === 401){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido un error intentado obtener las reseñas. Intentalo más tarde o reporta el problema.')
        })
    }, [])

    useEffect(() => {
        console.log(scrollToBottom)
        if(scrollToBottom && getReviewsControl){
            setLoading(true)
            getReviews().then(({ reviews }) => {
                setLoading(false)
                if(reviews.length === 0){ return setGetReviewsControl(false) }
                sortReviews(reviews)
            }).catch(e => {
                console.log(e)
                setLoading(false)
                if(e.response?.status === 401){ return setTokenExpirate(true) }
                setErr(true)
                setMessage('Ha ocurrido un error intentado obtener las reseñas. Intentalo más tarde o reporta el problema.')
            })
        }
    }, [scrollToBottom])

    return (
        <Content
            flexDirection="column"
            width="100%"
            alignItems="center"
        >
            {reviews.length !== 0 ? reviewsGroup.map((reviewGroup, idx) => (
                <ReviewsGroup 
                    key={idx}
                    reviews={reviewGroup.reviews}
                    title={reviewGroup.title}
                    statusReview={statusReview}
                    itemHeight={9}
                />
            )) : <EmptyWarning>No hay ninguna reseña por aprobar</EmptyWarning>}
            {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
            {err && <MessageError>{message}</MessageError>}
            <TokenExpirate modal={tokenExpirate} admin/>
        </Content>
    )
}

export default Reviews
