import React, { useState, useEffect } from 'react'

import AdminValidator from '../components/AdminValidator'
import CalendarView from '../components/doctor/CalendarView'
import Patients from '../components/patients/View'
import DateInfo from '../components/dateInfo/View'
import Edit from '../components/userEdit/View'
import Schedule from '../components/schedule/View'

function Doctor() {
 
    const [userType, setUserType] = useState(null) 
    const [path, setPath] = useState(null)
    const [scrollToBottom, setScrollToBottom] = useState(false)

    const scrollBottom = e => {
        const bottom = e.target.scrollHeight - e.target.scrollTop === e.target.clientHeight;
        if(bottom){ setScrollToBottom(true) }
        else{ setScrollToBottom(false) }
    }

    useEffect(() => {
        const path = window.location.pathname.split('/')[3]

        if(!path){ 
            setPath('calendar') 
            document.getElementById('root').style.overflowY = "hidden"
        }
        else if(path === 'pacientes'){ 
            setPath('patients'); 
            document.getElementById('root').style.overflowY = "auto" 
        }
        else if(path === 'aprobar'){ 
            setPath('aprove') 
            document.getElementById('root').style.overflowY = "auto" 
        }
        else if(path === 'cita'){ 
            setPath('date') 
            document.getElementById('root').style.overflowY = "hidden"
        }
        else if(path === 'editar'){ 
            setPath('edit') 
            document.getElementById('root').style.overflowY = "hidden"
        }
        else if(path === 'horario'){ 
            setPath('schedule') 
            document.getElementById('root').style.overflowY = "hidden"
        }
        setUserType(window.location.pathname.split('/')[2].toUpperCase())
    }, [])

    return (
        userType && (
            <AdminValidator 
                userType={userType} 
                height={(path === "patients" || path === "aprove") ? '100%' : null}  
                onScroll={scrollBottom}
            >
                {path === "calendar" && <CalendarView userType={userType}/>}
                {path === "patients" && <Patients scrollToBottom={scrollToBottom} userType={userType}/>}
                {path === "aprove" && <Patients scrollToBottom={scrollToBottom} aproveDates userType={userType} />}
                {path === "date" && <DateInfo userType={userType}/>}
                {path === "edit" && <Edit />}
                {path === "schedule" && <Schedule userType={userType}/>}
            </AdminValidator>
        )
        
    )
}

export default Doctor
