import React, { useState, useEffect } from 'react'

import { ScheduleHour, ScheduleDateContainer } from '../../styles/Doctor'

function HourItem({ hour, dates, seeInfo }) {

    let interval = null

    const [expandItem, setExpandItem] = useState(false)
    const [alignItemControl, setAlignItemControl] = useState(false)

    const isDate = () => {
        return (
            dates.some( date => new Date(date.fecha).getHours() === Number(hour.split(':')[0]) && 
            new Date(date.fecha).getUTCMinutes() === Number(hour.split(':')[1]) )
        )
    }

    const atHome = () => {
        let date = dates.find( date => new Date(date.fecha).getHours() === Number(hour.split(':')[0]) && 
        new Date(date.fecha).getUTCMinutes() === Number(hour.split(':')[1]) )
        return date?.direccion
    }

    const getPatient = () => {
        let date = dates.find( date => new Date(date.fecha).getHours() === Number(hour.split(':')[0]) && 
        new Date(date.fecha).getUTCMinutes() === Number(hour.split(':')[1]) )
        return date?.nombre
    }

    const handleClick = () => {
        const isDate = dates.some( date => new Date(date.fecha).getHours() === Number(hour.split(':')[0]) && 
        new Date(date.fecha).getUTCMinutes() === Number(hour.split(':')[1]) )

        if(isDate){ 
            setExpandItem(!expandItem)
            setAlignItemControl(true)
        }
    }

    const getInfo = () => {
        let date = dates.find( date => new Date(date.fecha).getHours() === Number(hour.split(':')[0]) && 
        new Date(date.fecha).getUTCMinutes() === Number(hour.split(':')[1]) )

        if(date){
            return (
                <>
                <p>Edad: {date?.edad} &nbsp;&nbsp;&nbsp;&nbsp;Pagado: {date?.pagado ? 'Si' : 'No'}</p>
                {date?.direccion && <p>Dirección: {date?.direccion}</p>}
                {date?.servnombre && <p>Laboratorios: {date?.servnombre.lengtht > 30 ? `${date?.servnombre.slice(0,30)}...` : date?.servnombre}</p>}
                {date?.padecimiento && <p>Padecimiento: {date?.padecimiento.length > 30 ? `${date?.padecimiento.slice(0,30)}...` : date?.padecimiento}</p>}
                <span className="see-more" onClick={handleSeeInfo}>Ver más</span>
                </>
            )
        }
    }

    const handleSeeInfo = () => {
        let date = dates.find( date => new Date(date.fecha).getHours() === Number(hour.split(':')[0]) && 
        new Date(date.fecha).getUTCMinutes() === Number(hour.split(':')[1]) )

        seeInfo(date)
    }

    useEffect(() => {
        interval = setTimeout(() => setAlignItemControl(expandItem ? true : false), 500); 
        return () => clearTimeout(interval)
    }, [expandItem])

    return (
        <ScheduleHour 
            bColor={isDate() ? atHome() ? 'rgb(88,32, 142)' : 'rgb(0,47,91)' : 'white'}
            cursor={isDate() ? 'pointer' : 'default'}
            alignItems={alignItemControl ? 'flex-start' : 'center'}
            mTop={alignItemControl ? '0.5vw' : '0'}
        >
            <span onClick={handleClick}>{hour}</span>
            <ScheduleDateContainer 
                bColor={isDate() ? atHome() ? 'rgb(88,32, 142)' : 'rgb(0,47,91)' : 'rgb(240, 240, 240)'}
                height={atHome() ? expandItem ? '7vw' : '1.5vw' : expandItem ? '5.5vw' : '1.5vw'}
            >
                <div className="line" />
                <div className="content">
                    {getPatient()}
                    <br />
                    {getInfo()}
                </div>
            </ScheduleDateContainer>
        </ScheduleHour>
    )
}

export default HourItem
