import React from 'react'

import { InfoItem } from '../../styles/User'

function Input({ idx, children, removeInput, value, onChange, hasRecipe }) {

    const handleInput = e => onChange(idx - 1, e.target.value)

    const removeItem = e => {
        e.preventDefault()
        removeInput(idx - 1)
    }

    return (
        <InfoItem 
            alignItems="center" 
            justifyContent="flex-start"
            mInput="0 1vw"
            labelWidth="2vw"
        >
            {hasRecipe ? (
                <>
                <span>{idx}. </span>
                <p>{value}</p>
                </>
            ) : (
                <>
                <label htmlFor={`input${idx}`}>{idx}. </label>
                <div className="input-container">
                    <input 
                        id={`input${idx}`}
                        value={value}
                        onChange={handleInput}
                    />
                    <button onClick={removeItem}>x</button>
                </div>
                </>
            )}
            
            {children}
        </InfoItem>
    )
}

export default Input
