import React from 'react'
import { BrowserRouter, Switch, Route } from "react-router-dom";

import Home from './views/Home'
import Doctors from './views/Doctors'
import Info from './views/Info'
import Labs from './views/Labs'
import Date from './views/Date'
import SignUp from './views/SignUp'
import User from './views/User'
import ForgotPassword from './views/ForgotPassword'
import AdminLogin from './views/AdminLogin'
import Admin from './views/Admin'
import Doctor from './views/Doctor'
import Payment from './views/Payment'
import CreatePassword from './views/CreatePassword'
import Provider from './context/Provider'

function Router() {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route exact path="/medicos" component={Doctors} />
                <Route exact path="/medicos/:id" component={Info} />
                <Route exact path="/laboratorios" component={Labs} />
                <Route exact path="/laboratorios/:id" component={Info} />
                <Route exact path="/admin/login" component={AdminLogin} />
                <Route exact path="/admin" component={Admin} />
                <Route exact path="/admin/resenas" component={Admin} />
                <Route exact path="/admin/usuarios" component={Admin} />
                <Route exact path="/admin/usuarios/:id" component={Admin} />
                <Route exact path="/admin/medico/pacientes" component={Doctor} />
                <Route exact path="/admin/medico/aprobar" component={Doctor} />
                <Route exact path="/admin/medico/editar" component={Doctor} />
                <Route exact path="/admin/medico/horario" component={Doctor} />
                <Route exact path="/admin/medico/cita/:id" component={Doctor} />
                <Route exact path="/admin/lab/pacientes" component={Doctor} />
                <Route exact path="/admin/lab/aprobar" component={Doctor} />
                <Route exact path="/admin/lab/editar" component={Doctor} />
                <Route exact path="/admin/lab/horario" component={Doctor} />
                <Route exact path="/admin/lab/cita/:id" component={Doctor} />
                <Route exact path="/admin/asistente/pacientes" component={Doctor} />
                <Route exact path="/admin/asistente/aprobar" component={Doctor} />
                <Route exact path="/admin/asistente/editar" component={Doctor} />
                <Route exact path="/admin/asistente/horario" component={Doctor} />
                <Route exact path="/admin/asistente/cita/:id" component={Doctor} />
                <Route exact path="/admin/login" component={AdminLogin} />
                <Route exact path="/usuario" component={User} />
                <Route exact path="/cita/pago" render={(props) => <Payment {...props}/>} />
                <Route exact path="/recuperar" render={(props) => <ForgotPassword {...props}/>}  />
                <Route exact path="/registro" render={(props) => <SignUp {...props}/>}  />
                <Route exact path="/admin/password" render={(props) => <CreatePassword {...props}/>} />
                <Route exact path="/admin/medico" render={(props) => (
                    <Provider>
                        <Doctor {...props}/>
                    </Provider>
                )} /> 
                <Route exact path="/admin/lab" render={(props) => (
                    <Provider>
                        <Doctor {...props}/>
                    </Provider>
                )} /> 
                <Route exact path="/admin/asistente" render={(props) => (
                    <Provider>
                        <Doctor {...props}/>
                    </Provider>
                )} /> 
                <Route exact path="/cita" render={(props) => (
                    <Provider>
                        <Date {...props}/>
                    </Provider>
                )} />
            </Switch>
        </BrowserRouter>
    )
}

export default Router
