import React, { useEffect, useState } from 'react'
import axios from 'axios'
import Cookies from 'js-cookie';

import Navbar from '../components/Navbar'
import InfoContainer from '../components/info/InfoContainer'
import Reviews from '../components/info/Reviews'

import { MainContainer } from '../styles/Info'

import { URL } from '../variables'

function Info() {

    const infoType = window.location.pathname.split('/')[ window.location.pathname.split('/').length - 2 ] === 'medicos' ? 'medico' : 'laboratorio'

    const [info, setInfo] = useState({})
    const [reviews, setReviews] = useState([])
    const [monday, setMonday] = useState([])
    const [tuesday, setTuesday] = useState([])
    const [wednesday, setWednesday] = useState([])
    const [thursday, setThursday] = useState([])
    const [friday, setFriday] = useState([])
    const [saturday, setSaturday] = useState([])
    const [sunday, setSunday] = useState([])
    const [isLogged, setIsLogged] = useState(false)

    const getInfo = async (id) => {
        let res = await axios({
            url: `${URL}/${infoType}/${id}?reviews=true&schedule=true`,
            method: 'GET',
            timeout: 5000
        })
        return res.data
    }

    useEffect(() => {
        let id = window.location.pathname.split('/')[ window.location.pathname.split('/').length - 1 ]
        if(Cookies.get('payload')){ setIsLogged(true) }
        getInfo(id).then( ({ info, reviews, monday, tuesday, wednesday, thursday, friday, saturday, sunday }) => {
            setInfo(info)
            setReviews(reviews)
            setMonday(monday)
            setTuesday(tuesday)
            setWednesday(wednesday)
            setThursday(thursday)
            setFriday(friday)
            setSaturday(saturday)
            setSunday(sunday)
        }).catch( e => {
            if(e.response.status === 400){ 
                if(infoType === "medico"){ window.location.replace('/medicos')  }
                else{ window.location.replace('/laboratios')  }
            }
        })
    }, []) 

    return (
        <MainContainer>
            <Navbar bgNavbar={true}/>
            <InfoContainer 
                info={info} 
                infoType={infoType}
                monday={monday}
                tuesday={tuesday}
                wednesday={wednesday}
                thursday={thursday}
                friday={friday}
                saturday={saturday}
                sunday={sunday}
            >
                <Reviews 
                    reviews={reviews} 
                    itemID={info.id}
                    infoType={infoType}
                    isLogged={isLogged}
                />
            </InfoContainer>
        </MainContainer>
    )
}

export default Info
