import React, { useState } from 'react'
import axios from 'axios';
import Loader from 'react-loader-spinner'

import PromInfo from '../date/PromInfo'
import ForgotPassword from './ForgotPassword'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario.svg';

import { LoginFormContainer, LoginForm, LoginTitle, LoginFormLabel, LoginInput, 
    LoginFormBtn, LoginFormForgotPasswordContainer, LoginFormForgotPassword, LoginMessage } from '../../styles/Home'

import { URL } from '../../variables'

function Login({ isLogged, height, admin, autoFocus, adminEdit }) {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [message, setMessage] = useState('')
    const [err, setErr] = useState(false)
    const [modal, setModal] = useState(false)
    const [loading, setLoading] = useState(false)

    const handleUsername = e => { setEmail(e.target.value)}
    const handlePassword = e => setPassword(e.target.value)

    const login = async (data) => {
        const res = await axios({
            url: `${URL}/${admin ? 'admin/' : ''}login`,
            method: 'POST',
            data
        })

        return res.data
    }

    const handleBtn = (e) => {
        e.preventDefault()
        const { ok, message } = checkData()
        if(ok){
            const data = { email, password }
            setLoading(true)
            setErr(false)
            login(data).then(({ userType }) => {
                setLoading(false)
                if(admin){
                    if(userType === "ADMIN"){ window.location.replace('/admin') }
                    if(userType === "MEDICO"){ window.location.replace('/admin/medico') }
                    if(userType === "LAB"){ window.location.replace('/admin/lab') }
                    if(userType === "ASISTENTE"){ window.location.replace('/admin/asistente') }
                    return 
                }else{ window.location.reload() }
            }).catch((e) => {
                setErr(true)
                setLoading(false)
                if(e.response.status === 400){ return setMessage('Email o contraseña incorrectos') }
                setMessage('Ha ocurrido un error en el servidor. Intentalo más tarde') 
            })
        }else{
            setErr(true)
            setMessage(message)
        }
    }

    const checkData = () => {
        if(email === "" || password === ""){ return { ok: false, message: 'Debes completar todos los campos' } }
        if(!checkEmail()){ return { ok: false, message: 'El email debe de ser valido' } }
        return { ok: true }
    }

    const checkEmail = () => {
        const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
    }

    const openModal = () => setModal(true)

    return (
        <LoginFormContainer height={height}>
            {!admin && (
                <>
                <PromInfo 
                    width="60%" 
                    sizeTBold={4}
                    sizeTNormal="2vw"
                    lineWidth="30%"
                    adminEdit={adminEdit}
                />
                <ForgotPassword 
                    modal={modal} 
                    setModal={setModal}                
                />
                </>
            )}
            
            {!isLogged && (
                <LoginForm id="login" height={admin ? '47vh' : '53vh'}>
                    <UserIcon />
                    <LoginTitle>INICIO DE SESIÓN</LoginTitle>
                    <LoginFormLabel htmlFor="email">EMAIL</LoginFormLabel>
                    <LoginInput 
                        onChange={handleUsername}
                        value={email}
                        type="email"
                        autoFocus={autoFocus}
                        id="email"
                    />
                    <LoginFormLabel htmlFor="password">CONTRASEÑA</LoginFormLabel>
                    <LoginInput 
                        onChange={handlePassword}
                        value={password}
                        type="password"
                        id="password"
                    />
                    <LoginFormBtn onClick={handleBtn} mgBottom="1vw">ACEPTAR</LoginFormBtn>
                    {!admin && (
                        <>
                        <LoginFormForgotPasswordContainer>
                            <LoginFormForgotPassword onClick={openModal}>Recuperar contraseña</LoginFormForgotPassword>
                            <LoginFormForgotPassword href={`${URL}/registro`}>Crear nueva cuenta</LoginFormForgotPassword>
                        </LoginFormForgotPasswordContainer>
                        </>
                    )}
                    {err && <LoginMessage>{message}</LoginMessage>}
                    {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
                </LoginForm>
            )}
        </LoginFormContainer>
    )
}

export default Login
