import React, { useState, useEffect } from 'react'
import axios from 'axios';

import InfoDate from '../user/Info'
import Form from './Form'
import InfoContainer from './InfoContainer'
import TokenExpirate from '../TokenExpirate'

import { Content, MessageError } from '../../styles/Admin'

import { URL } from '../../variables'

function View({ userType }) {
    
    const [date, setDate] = useState({})
    const [recipe, setRecipe] = useState([])
    const [err, setErr] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [message, setMessage] = useState('')
    const [id, setId] = useState('')


    const getDate = async id => {
        const res = await axios({
            url: `${URL}/cita/${id}`,
            method: 'GET'
        })

        return res.data
    }

    const createRecipe = () => setRecipe(!recipe)

    const setLabs = dates => {
        let nDate = {}
        dates.forEach(date => {
            if(JSON.stringify(nDate) === "{}"){ nDate = date }
            else{
                let newServnombre = nDate.servnombre
                nDate.servnombre = `${newServnombre}, ${date.servnombre}`
            }
        })
        setDate(nDate)
    }

    useEffect(() => {
        let id = window.location.pathname.split('/')[ window.location.pathname.split('/').length - 1 ]
        getDate(id).then( ({ date, recipe }) => {
            if(userType === "LAB"){ return setLabs(date) }
            setDate(date[0])
            setRecipe(recipe)
            setId(id)
        }).catch( e => {
            console.log(e)
            if(e.response.status === 400){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido intentado obtener esta cita. Intentalo más tarde o reporta el problema')
        })
    }, [])

    return (
        <Content 
            alignItems="center" 
            justifyContent="space-evenly" 
            flexDirection="column" 
            width="100%"
        >
            <InfoContainer userType={userType}>
                <InfoDate   
                    user={date}
                    fullDescription
                    fontSize="1.2vw"
                    userType={userType}
                    createRecipe={createRecipe}
                />
            </InfoContainer>
            {userType === "MEDICO" ? (
                <Form 
                    id={id}
                    recipe={recipe}
                />
            ) :
            userType === "LAB" && <> </> }
            {err && <MessageError>{message}</MessageError>}
            <TokenExpirate modal={tokenExpirate} admin/>
        </Content>
    )
}

export default View
