import React from 'react'

import { DefaultImage, InfoContainer, InfoItem, Image, EditBtn, ImageContainer, BtnSeeMoreDate } from '../../styles/User'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario_imagen_default.svg';

function Info({ user, imageSize, iconSize, width, height, itemMLeft, fontSize, fullDescription, showBtn, userType }) {

    const handleBtn = () => window.location.replace(`/admin/${userType.toLowerCase()}/cita/${user.cita || user.id}`)

    const twoDigits = number => number < 10 ? `0${number}` : number

    const getDate = () => {
        const nDate = new Date(user.fecha)
        return `${twoDigits(nDate.getDate())}/${twoDigits(nDate.getMonth() + 1)}/${nDate.getFullYear()}`
    }

    return ( 
        <>
            <ImageContainer>
                {user?.imagen ? (
                    <Image size={imageSize} src={user?.imagen} />
                ):(
                    <DefaultImage size={imageSize}>
                        <UserIcon width={iconSize || '10vw'} height={iconSize || '10vw'}/>
                    </DefaultImage>
                )}
                
            </ImageContainer> 
            <InfoContainer width={width} height={height}>
                <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                    <span>Nombre:</span>
                    <p>{user?.nombre}</p>
                </InfoItem>
                {user?.cargo && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Especialidad:</span>
                        <p>{user?.cargo}</p>
                    </InfoItem>
                )}
                {user?.fecha && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Fecha:</span>
                        <p>{getDate()}</p>
                    </InfoItem>
                )}
                {user?.edad && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Edad:</span>
                        <p>{user?.edad}</p>
                    </InfoItem>
                )}
                {user?.sangre && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Tipo de sangre:</span>
                        <p>{user?.sangre} {user?.rh}</p>
                    </InfoItem>
                )}
                {(user?.direccion || user?.clienteDireccion) && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Dirección:</span>
                        <p>{user?.direccion || user?.clienteDireccion}</p>
                    </InfoItem>
                )}
                {user?.phone && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Teléfono:</span>
                        <p>{user?.telefono}</p>
                    </InfoItem>
                )}
                {user?.email && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Email:</span>
                        <p>{user?.email}</p>
                    </InfoItem>
                )}
                {user?.enfermedades && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Enfermedades:</span>
                        <p>{user?.enfermedades}</p>
                    </InfoItem>
                )}
                {user?.enfermedadesFam && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Antecedentes familiares:</span>
                        <p>{user?.enfermedadesFam}</p>
                    </InfoItem>
                )}
                {user?.padecimiento && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Padecimiento:</span>
                        <p>{fullDescription ? user.padecimiento : user.padecimiento.length > 40 ? `${user.padecimiento.slice(0,40)}...` : user.padecimiento}</p>
                    </InfoItem>
                )}
                {user?.servnombre && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Laboratorios:</span>
                        <p>{fullDescription ? user?.servnombre : user.servnombre.length > 40 ? `${user.servnombre.slice(0,40)}...` : user.servnombre}</p>
                    </InfoItem>
                )}
                {user?.contacto && (
                    <InfoItem itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Contacto:</span>
                        <p>{user?.contacto}</p>
                    </InfoItem>
                )}
                {user?.alergias && (
                    <InfoItem mgBottom={user?.padecimiento ? '0.5vw' : '3vw'} itemMLeft={itemMLeft} fontSize={fontSize}>
                        <span>Alergias:</span>
                        <p>{user?.alergias}</p>
                    </InfoItem>
                )}

                {(user?.padecimiento || user?.servnombre) && showBtn && <BtnSeeMoreDate onClick={handleBtn}>VER CITA</BtnSeeMoreDate>}
            </InfoContainer>
        </>
    )
}

export default Info
