import React, { useContext, useState } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import { BtnFinishDate, ErrMessage } from '../../styles/Date'

import { URL } from '../../variables'
import { appContext } from '../../context/Provider'

function BtnDate({ name, age, illness, phone, email, address, homeService, clientID }) {

    const context = useContext(appContext)

    const [err, setErr] = useState(false)
    const [success, setSuccess] = useState(false)
    const [loading, setLoading] = useState(false)
    const [errMessage, setErrMessage] = useState('')

    const postDate = async (data) => {
        const res = await axios({
            url: `${URL}/cita`,
            method: 'POST',
            data
        })

        return res.data
    }

    const handleBtn = () => {
        if(checkData()){
            let data = {
                date: context.date, type: context.dateType, address,
                name, age: Number(age), illness, phone, email, homeService,
            }
            if(context.dateType === "medico"){ data.doctor = { name: context.doctorDate.nombre, id: context.doctorDate.id } }
            else{ data.labs = context.labsDate.map( item => {  return { id: item.id, name: item.nombre } }) }

            if(clientID !== ""){ data.clientID = clientID }

            setLoading(true)

            postDate(data).then(({ ok, message }) => {
                setLoading(false)
                if(!ok){
                    setErrMessage('Ya existe una cita para esta fecha y hora.')
                    if(message){ setErrMessage(message) }
                    return setErr(true)
                }
                setErrMessage('Cita agendada. Espere la confirmación en su correo.')
                setSuccess(true)
            }).catch((e) => {
                setLoading(false)
                if(e.response.status === 400){
                    setErrMessage('Debes completar todos los campos.')
                    setErr(true)
                    return
                }
                setErrMessage('Ha ocurrido un error en el servidor. Vuelve a intentarlo más tarde.')
                setErr(true)
            })
        }
    }

    const checkData = () => {
        if(name === "" || age === "" || (illness === "" && context.dateType === "doctor") || phone === "" || email === "" || 
        (homeService && address === "" && context.dateType === "doctor") || (JSON.stringify(context.doctorDate) === "{}" && context.labsDate.length === 0)){
            setErrMessage('Debes completar todos los campos.')
            setErr(true)
            return false
        }
        setErrMessage('')
        setErr(false)
        return true
    }

    return (
        <>
            <BtnFinishDate onClick={handleBtn}>HACER CITA</BtnFinishDate>
            {(err || success) && <ErrMessage color={err ? 'red' : 'rgb(0,47,91)'} mTop="0">{errMessage}</ErrMessage>}
            {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
        </>
    )
}

export default BtnDate
