import React from 'react'
import Modal from 'react-modal';

import { TokenExpirateModal } from '../../styles/Admin' 
import { BtnContainer } from '../../styles/User' 

Modal.setAppElement('#root')

const customStyles = {
    content : {
        top: '35%',
        left: '30%',
        right: 'auto',
        bottom: 'auto',
        width: '40%',
        height: '30%',
        borderRadius: '0',
        border: '0',
    }
};

function ModalDeleteUser({ modal, onClose, deleteItem }) {
    return (
        <Modal
            isOpen={modal}
            onRequestClose={onClose}
            style={customStyles}
            contentLabel="Example Modal"
        >
            <TokenExpirateModal>
                <h1>¿Está seguro que desea borrar este usuario?</h1>
                <BtnContainer>
                    <button onClick={onClose}>CANCELAR</button>
                    <button onClick={deleteItem}>ACEPTAR</button>
                </BtnContainer>
            </TokenExpirateModal>
        </Modal>
    )
}

export default ModalDeleteUser
