import React, { useState } from 'react'
import axios from 'axios'

import ModalRejectDate from './ModalRejectDate'

import { PatientItemContainer } from '../../styles/Doctor'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario_imagen_default.svg';

import { URL } from '../../variables'

function PatientItem({ idx, date, onClick, aproveDates, setSessionExpirate, removeDate, setLoading }) {

    const [modal, setModal] = useState(false)

    const twoDigits = number => number < 10 ? `0${number}` : number

    const getHour = () => `${twoDigits(new Date(date.fecha).getHours())}:${twoDigits(new Date(date.fecha).getMinutes())}`

    const getDate = () => {
        const nDate = new Date(date.fecha)
        return `${twoDigits(nDate.getDate())}/${twoDigits(nDate.getMonth() + 1)}/${nDate.getFullYear()} ${getHour()}`
    }

    const handleClick = () => onClick(date)

    const changeDate = async (aprove) => {
        const res = await axios({
            url: `${URL}/cita/status/${date.id}`,
            method: 'PUT',
            data: { aprove, ...date }
        })

        return res.data
    }

    const aproveDate = () => {
        setLoading(true)
        changeDate(true).then(() => {
            removeDate(date.id)
            setLoading(false)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response.status === 401){ return setSessionExpirate(true) }
        })
    }

    const rejectDate = () => {
        setLoading(true)
        changeDate(false).then(() => {
            removeDate(date.id)
            setLoading(false)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response.status === 401){ return setSessionExpirate(true) }
        })
    }
    
    const openModal = () => setModal(true)

    const closeModal = () => setModal(false)

    return (
        <PatientItemContainer bgColor={idx%2 !== 0 ? 'rgb(235,235,235)' : 'rgb(245,245,245)'}>
            <ModalRejectDate 
                modal={modal} 
                closeModal={closeModal} 
                handleBtn={rejectDate}
            />
            {!date.warning ? (
                <>
                <div>
                    {date.imagen ? <img src={date.imagen}/>:(
                        <div className="image-default">
                            <UserIcon width="1vw" height="1vw"/>
                        </div>
                    )}
                    <span>{date.nombre}</span>
                </div>
                {date.servnombre && (
                    <div className="service-name">
                        <span>{date.servnombre}</span>
                    </div>
                )}
                <div>
                    <span>{date.cliente ? getDate() : getHour()}</span>
                </div>
                <div>
                    <span>Pagado: {date.pagado ? 'Si' : 'No'}</span>
                </div>
                {aproveDates ? (
                    <div>
                        <button onClick={aproveDate}>APROBAR</button>
                        <button onClick={openModal}>RECHAZAR</button>
                    </div>
                ) : <span className="see-more" onClick={handleClick}>Ver más...</span> }
                </>
            ):(
                <div className="warning-text">
                    <span >{date.text}</span>
                </div>
            )}
        </PatientItemContainer>
    )
}

export default PatientItem
