import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import PatientsGroup from './PatientsGroup'
import Filter from './Filter'
import TokenExpirate from '../TokenExpirate'

import { Content, MessageError } from '../../styles/Admin'

import { URL } from '../../variables'

function View({ scrollToBottom, aproveDates, userType }) {

    const [dates, setDates] = useState([])
    const [datesGroup, setDatesGroup] = useState([])
    const [loading, setLoading] = useState(false)
    const [getDatesControl, setGetDatesControl] = useState(true)
    const [sessionExpirate, setSessionExpirate] = useState(false)
    const [messageErr, setMessageErr] = useState(null)

    const twoDigits = number => number < 10 ? `0${number}` : number

    const fetchDatesByDate = async (clearFilter) => {
        const res = await axios({
            url: `${URL}/cita/history?start=${clearFilter ? 0 : dates.length}${aproveDates ? '&unaproved=true' : ''}`,
            method: 'GET'
        })

        return res.data
    }

    const fetchDatesByName = async (clearFilter) => {
        const res = await axios({
            url: `${URL}/cita/patients?start=${clearFilter ? 0 : dates.length}`,
            method: 'GET'
        })

        return res.data
    }

    const handleItem = date => window.location.replace(`/admin/medico/cita/${date.id}`)    

    const addDates = (nDates) => {
        const newDates = [...dates, ...nDates]
        setDates(newDates)
    }

    const sortDatesByDate = (nDates, clearFilter, query) => {
        let newDatesGroup = clearFilter ? [] : [...datesGroup]

        if(nDates.length === 0){
            const date = new Date(Number(query))
            newDatesGroup.push({
                title: query ? `CITAS ${twoDigits(date.getDate())}/${twoDigits(date.getMonth()+1)}/${date.getFullYear()}` : 'CITAS',
                dates: [{
                    text: query ? 'No se ha encontrado ningúna cita que cumpla con la busqueda.' : 'Todavía no se tienen citas registradas',
                    warning: true
                }]
            })
            return setDatesGroup(newDatesGroup)
        }

        nDates.forEach( (nDate) => {
            const date = new Date(nDate.fecha)

            let newDatesGroupIdx = newDatesGroup.length-1
            let datesGroupDatesIdx = newDatesGroup[ newDatesGroupIdx ]?.dates.length - 1
            
            if(newDatesGroup[ newDatesGroup.length-1 ]?.year === date.getFullYear() && 
                newDatesGroup[ newDatesGroup.length-1 ]?.month === date.getMonth() &&
                newDatesGroup[ newDatesGroup.length-1 ]?.day === date.getDate() ){
                    if(userType === "LAB"){
                        if(newDatesGroup[ newDatesGroupIdx ].dates[datesGroupDatesIdx].cita === nDate.cita){
                            let newServnombre = newDatesGroup[ newDatesGroupIdx ].dates[datesGroupDatesIdx].servnombre
                            newDatesGroup[ newDatesGroupIdx ].dates[datesGroupDatesIdx].servnombre = `${newServnombre}, ${nDate.servnombre}`
                        }else{ newDatesGroup[ newDatesGroup.length-1 ].dates.push(nDate) }
                    }else{ newDatesGroup[ newDatesGroup.length-1 ].dates.push(nDate) }
                    
            }else{
                newDatesGroup.push({
                    title: `CITAS ${twoDigits(date.getDate())}/${twoDigits(date.getMonth()+1)}/${date.getFullYear()}`,
                    year: date.getFullYear(),
                    month: date.getMonth(),
                    day: date.getDate(),
                    dates: [nDate]
                })
            }
        })
        setDatesGroup(newDatesGroup)
    }

    const sortDatesByName = (nDates, clearFilter, query) => {
        let newDatesGroup = clearFilter ? [] : [...datesGroup]

        if(nDates.length === 0){
            newDatesGroup.push({
                title: `${query.toUpperCase()}`,
                dates: [{
                    text: 'No se ha encontrado ningúna cita que cumpla con la busqueda.',
                    warning: true
                }]
            })
            return setDatesGroup(newDatesGroup)
        }

        nDates.forEach( nDate => {
            if(newDatesGroup[ newDatesGroup.length-1 ]?.cliente === nDate.cliente ){ newDatesGroup[ newDatesGroup.length-1 ].dates.push(nDate) }
            else{
                newDatesGroup.push({
                    title: `${nDate.nombre}`,
                    cliente: nDate.cliente,
                    dates: [nDate]
                })
            }
        })
        setDatesGroup(newDatesGroup)
    }

    const setDatesFiltered = (dates, orderType, query) => {
        setDates(dates)
        if(orderType === "date"){ sortDatesByDate(dates, true, query) }
        else{ sortDatesByName(dates, true, query) }
        
    }

    const getDatesByDate = (clearFilter, checkDatesLength) => {
        setLoading(true)
        fetchDatesByDate(clearFilter).then(({ dates }) =>{
            setLoading(false)
            setMessageErr(null)
            if(dates.length === 0 && checkDatesLength){ return setGetDatesControl(false) }
            addDates(dates)
            sortDatesByDate(dates, clearFilter)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response.status === 401){ return setSessionExpirate(true) }
            if(e.response.status === 400){ return setMessageErr('No estás realizando correctamente está operación.') }
            setMessageErr('Ha ocurrido un error en el servidor, intentalo otra vez o reporta el problema.')
        })
    }

    const getDatesByName = (clearFilter) => {
        setLoading(true)
        fetchDatesByName(clearFilter).then(({ dates }) =>{
            setLoading(false)
            setMessageErr(null)
            if(dates.length === 0){ return setGetDatesControl(false) }
            addDates(dates)
            sortDatesByName(dates, clearFilter)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response.status === 401){ return setSessionExpirate(true) }
            if(e.response.status === 400){ return setMessageErr('No estás realizando correctamente está operación.') }
            setMessageErr('Ha ocurrido un error en el servidor, intentalo otra vez o reporta el problema.')
        })
    }

    const changeOrder = (orderType) => {
        if(orderType === "date"){ getDatesByDate(true) }
        else{ getDatesByName(true) }
    }

    const removeDate = id => {
        let newDates = [...dates]
        newDates.splice(id, 1)
        setDates(newDates)
        getDatesByDate(true)
    }

    useEffect(() => { getDatesByDate(true) }, [])

    useEffect(() => {
        if(scrollToBottom && getDatesControl){ getDatesByDate(null, true) }
    }, [scrollToBottom])

    return (
        <Content
            flexDirection="column"
            width="100%"
            alignItems="center"
        >
            {!aproveDates && (
                <Filter 
                    setDates={setDatesFiltered}
                    cancelFilter={changeOrder}
                    setSessionExpirate={setSessionExpirate}
                    setMessageErr={setMessageErr}
                    changeOrder={changeOrder}
                />
            )}
            {datesGroup.map( dateGroup => (
                <PatientsGroup 
                    dates={dateGroup.dates}
                    title={dateGroup.title}
                    itemHeight={3}
                    handleItem={handleItem}
                    key={dateGroup.title + Math.random().toString()}
                    aproveDates={aproveDates}
                    setSessionExpirate={setSessionExpirate}
                    removeDate={removeDate}
                    setLoading={setLoading}
                />
            ) )}
            {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
            {messageErr && <MessageError>{messageErr}</MessageError>}
            <TokenExpirate modal={sessionExpirate} admin/>
        </Content>
    )
}

export default View
