import React, { useState, useEffect, useContext } from 'react'
import axios from 'axios';

import CalendarComponent from '../date/CalendarComponent'
import Schedule from './Schedule'
import InfoDate from '../user/Info'
import TokenExpirate from '../TokenExpirate'

import { Content, MessageError } from '../../styles/Admin'

import { appContext } from '../../context/Provider'

import { URL } from '../../variables'

function CalendarView({ userType }) {

    const context = useContext(appContext)

    const [date, setDate] = useState(Date.now())
    const [dates, setDates] = useState([])
    const [seeInfo, setSeeInfo] = useState(false)
    const [err, setErr] = useState(false)
    const [sessionExpirate, setSessionExpirate] = useState(false)
    const [message, setMessage] = useState('')
    const [info, setInfo] = useState({})

    const getDates = async date => {
        const res = await axios({
            url: `${URL}/cita/filter?date=${date}`,
            method: 'GET'
        })

        return res.data
    }

    const handleSeeInfo = info => {
        setSeeInfo(true)
        setInfo(info)
    }

    const sortDates = dates => {
        let nDates = []
        dates.forEach(date => {
            if(nDates[ nDates.length - 1 ]?.cita === date.cita){
                let lastName = nDates[ nDates.length - 1 ]?.servnombre
                nDates[ nDates.length - 1 ].servnombre = `${lastName}, ${date?.servnombre}`
            }else{ nDates.push(date) }
        })
        return nDates
    }

    useEffect(() => {
        if(context.date){ 
            setDate(context.date)
            getDates(context.date).then(({ dates }) => {
                if(userType === "LAB"){ return setDates(sortDates(dates)) }
                setDates(dates)
            }).catch((e) => {
                console.log(e)
                if(e.response.status === 401){ return setSessionExpirate(true) }
                setErr(true)
                setMessage('Ha ocurrido un error intentando obtener las citas de este día. Recarga la página o reporta el problema')
            })
        }
    }, [context.date])

    return (
        <Content flexDirection="column">
            <Content alignItems="flex-start" justifyContent="space-evenly" flexDirection="row">
                <CalendarComponent 
                    woHourSelector
                    width="40%"
                    minHeight="40vh"
                    calendarClassName="calendar-doctor"
                    tileClassName="tile-calendar-doctor"
                />
                <Schedule 
                    date={date}
                    dates={dates}
                    seeInfo={handleSeeInfo}
                    userType={userType}
                />
            </Content>
            {seeInfo && (
                <Content 
                    alignItems="flex-start" 
                    justifyContent="flex-start" 
                    flexDirection="row" 
                    width="80%"
                    height="18%"
                    margin="0.5vw 0 0 6.5%"
                >
                    <InfoDate 
                        user={info}
                        imageSize="8vw"
                        iconSize="4vw"
                        height="100%"
                        width="22%"
                        itemMLeft="1vw"
                        fontSize="0.9vw"
                        showBtn
                        userType={userType}
                    />
                </Content>
            )}
            {err && <MessageError>{message}</MessageError>}
            <TokenExpirate modal={sessionExpirate} admin/>
        </Content>
    )
}

export default CalendarView
