import React, { useState, useEffect } from 'react'

import AdminValidator from '../components/AdminValidator'
import Reviews from '../components/admin/Reviews'
import Users from '../components/admin/Users'
import Home from './Home'

function Admin() {

    const [path, setPath] = useState(null)

    const [scrollToBottom, setScrollToBottom] = useState(false)

    const scrollBottom = e => {
        const bottom = e.target.scrollHeight - e.target.scrollTop === e.target.clientHeight;
        if(bottom){ setScrollToBottom(true) }
        else{ setScrollToBottom(false) }
    }

    useEffect(() => {
        const path = window.location.pathname.split('/')[2]
        if(!path){ 
            setPath('editPage') 
            document.getElementById('root').style.overflowY = "hidden"
        }else if(path === 'resenas'){ 
            setPath('reviews'); 
            document.getElementById('root').style.overflowY = "auto" 
        }else if(path === 'usuarios'){ 
            setPath('users'); 
            document.getElementById('root').style.overflowY = "auto" 
        }
    }, [])

    return (
        <AdminValidator 
            userType="ADMIN" 
            onScroll={scrollBottom}
            height={(path === "reviews") ? '100%' : null}  
        >
            {path === "editPage" && <Home adminEdit/>}
            {path === "reviews" && <Reviews scrollToBottom={scrollToBottom}/>}
            {path === "users" && <Users />}
        </AdminValidator>
    )
}

export default Admin
