import React, { useState, useEffect } from 'react'
import axios from 'axios'

import TokenExpirate from '../TokenExpirate'
import MenuUsers from './MenuUsers'
import DoctorItem from '../doctors/DoctorItem'
import ModalDeleteUser from './ModalDeleteUser'
import CreateUser from './CreateUser'

import { ContentContainer, TitleContainer, ErrMessage } from '../../styles/Date'
import { UsersContainer } from '../../styles/Admin'

import { URL } from '../../variables'

function Users() {

    const [users, setUsers] = useState([])
    const [message, setMessage] = useState('')
    const [userType, setUserType] = useState('MEDICO')
    const [sessionExpirate, setSessionExpirate] = useState(false)
    const [err, setErr] = useState(false)
    const [modal, setModal] = useState(false)
    const [createUser, setCreateUser] = useState(false)
    const [itemID, setItemID] = useState(null)

    const handleCheckboxDoctors = () => setUserType('MEDICO')
    const handleCheckboxLabs = () => setUserType('LAB')
    const handleCheckboxAssistant = () => setUserType('ASISTENTE')

    const getUsers = async () => {
        const res = await axios({
            url: `${URL}/admin/users?type=${userType}`,
            method: 'GET'
        })

        return res.data
    }

    const deleteUser = async () => {
        const res = await axios({
            url: `${URL}/admin/users/${itemID}`,
            method: 'DELETE'
        })

        return res.data
    }

    const deleteItem = () => {
        deleteUser().then(() => {
            let newUsers =  [...users]
            const idx = newUsers.findIndex(user => user.id === itemID)
            newUsers.splice(idx, 1)
            setUsers(newUsers)
            setItemID(null)
            setModal(false)
        }).catch(e => {
            console.log(e)
            setModal(false)
            if(e.response?.status === 401){ return setSessionExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido un error intentando elimniar al usuario. Intentalo más tarde o reporta el problema.')
        })
    }

    const handleDeleteItem = (doctor) => {
        setItemID(doctor.id)
        setModal(true)
    }

    const exitCreateUser = () => setCreateUser(false)

    const handleCreateUser = () => setCreateUser(true)

    const closeModal = () => setModal(false)

    const handleItem = () => {}

    useEffect(() => {
        getUsers().then(({ users }) => {
            setUsers(users)
        }).catch(e => {
            console.log(e)
            if(e.response?.status === 401){ return setSessionExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido un error intentando obtener los usuarios. Intentalo más tarde o reporta el problema.')
        })
    }, [userType])

    return (
        <ContentContainer pTop="0">
            <TitleContainer><h1>USUARIOS</h1></TitleContainer>
            <UsersContainer>
                <MenuUsers 
                    handleCheckboxDoctors={handleCheckboxDoctors}
                    handleCheckboxLabs={handleCheckboxLabs}
                    handleCheckboxAssistant={handleCheckboxAssistant}
                    userType={userType}
                    createUser={handleCreateUser}
                />
                <div className="content">
                    {users.map(user => (
                        <DoctorItem 
                            doctor={user}
                            onClick={handleItem}
                            key={user.id}
                            itemWidth={25}
                            admin
                            deleteItem={handleDeleteItem}
                        />
                    ))}
                </div>
            </UsersContainer>
            {err && <ErrMessage color="red" width="100%">{message}</ErrMessage>}
            <TokenExpirate modal={sessionExpirate} admin/>
            <ModalDeleteUser 
                modal={modal} 
                onClose={closeModal}
                deleteItem={deleteItem}
            />
            <CreateUser 
                modal={createUser}
                closeModal={exitCreateUser}
                setSessionExpirate={setSessionExpirate}
            />
        </ContentContainer>
    )
}

export default Users
