import React, { useState, useEffect } from 'react'
import moment from 'moment'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import Day from './Day'
import TokenExpirate from '../TokenExpirate'

import { ScheduleTable, ScheduleTableContainer } from '../../styles/Doctor'
import { EditBtn, Message } from '../../styles/User'

import { URL } from '../../variables'

function Schedule() {

    const days = ['Lun', 'Mar', 'Mié', 'Jue', 'Vie', 'Sab', 'Dom']

    const startMoment = moment().set('minute', 0).set('hour', 0)
    const endMoment = moment().set('minute', 0).set('hour', 23)

    const [monday, setMonday] = useState([{ start: startMoment, end: endMoment }])
    const [tuesday, setTuesday] = useState([{ start: startMoment, end: endMoment }])
    const [wednesday, setWednesday] = useState([{ start: startMoment, end: endMoment }])
    const [thursday, setThursday] = useState([{ start: startMoment, end: endMoment }])
    const [friday, setFriday] = useState([{ start: startMoment, end: endMoment }])
    const [saturday, setSaturday] = useState([{ start: startMoment, end: endMoment }])
    const [sunday, setSunday] = useState([{ start: startMoment, end: endMoment }])
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [err, setErr] = useState(false)
    const [loading, setLoading] = useState(true)
    const [message, setMessage] = useState('Aasdasdasdasdasda')

    const getDayValues = (day) => {
        if(day === 'Lun'){ return monday }
        else if(day === 'Mar'){ return tuesday }
        else if(day === 'Mié'){ return wednesday }
        else if(day === 'Jue'){ return thursday }
        else if(day === 'Vie'){ return friday }
        else if(day === 'Sab'){ return saturday }
        else{ return sunday }
    }

    const getSetDayValues = (day) => {
        if(day === 'Lun'){ return setMonday }
        else if(day === 'Mar'){ return setTuesday }
        else if(day === 'Mié'){ return setWednesday }
        else if(day === 'Jue'){ return setThursday }
        else if(day === 'Vie'){ return setFriday }
        else if(day === 'Sab'){ return setSaturday }
        else{ return setSunday }
    }

    const addRow = day => {
        let newValues = [...getDayValues(day)]
        newValues.push({ start: newValues[newValues.length - 1].end, end: endMoment, id: null })
        let setValues = getSetDayValues(day)
        setValues(newValues)
    }

    const removeRow = day => {
        let newValues = [...getDayValues(day)]
        if(newValues.length !== 1){
            newValues.splice(newValues. length - 1, 1)
            let setValues = getSetDayValues(day)
            setValues(newValues)
        }
    }

    const saveSchedule = async data => {
        const res = await axios({
            url: `${URL}/admin/schedule`,
            method: 'POST',
            data
        })
        return res.data
    }

    const getSchedule = async () => {
        const res = await axios({
            url: `${URL}/admin/schedule`,
            method: 'GET'
        })
        return res.data
    }

    const handleBtn = () => {
        setErr(false)
        setLoading(true)
        saveSchedule({ monday, tuesday, wednesday, thursday, friday, saturday, sunday }).then(() => {
            window.location.reload()
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response?.status === 401){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('No se ha podido guardar los cambios debido a un error en el servidor. Intentalo más tarde o reporta el problema.')
        })
    }

    const setSchedule = dayArray => {
        if(dayArray.length !== 0){
            const newDaySchedule = dayArray.map(period => {
                const hourStart = Number(period.inicio.split(':')[0])
                const minuteStart = Number(period.inicio.split(':')[1])
                const hourEnd = Number(period.final.split(':')[0])
                const minuteEnd = Number(period.final.split(':')[1])
    
                const momentStart = moment().set('minute', minuteStart).set('hour', hourStart)
                const momentEnd = moment().set('minute', minuteEnd).set('hour', hourEnd)
    
                return { start: momentStart, end: momentEnd }
            })
    
            const setDaySchedule = getSetDayValues(days[dayArray[0]?.dia - 1 === -1 ? 6 : dayArray[0]?.dia - 1])
            setDaySchedule(newDaySchedule)
        }
    }

    useEffect(() => {
        getSchedule().then(({ monday, tuesday, wednesday, thursday, friday, saturday, sunday}) => {
            setLoading(false)
            setSchedule(monday)
            setSchedule(tuesday)
            setSchedule(wednesday)
            setSchedule(thursday)
            setSchedule(friday)
            setSchedule(saturday)
            setSchedule(sunday)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.response?.status === 401){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('No se ha podido obtener el horario debido a un error en el servidor. Intentalo más tarde o reporta el problema.')
        })
    }, [])

    return (
        <ScheduleTableContainer>
            <ScheduleTable>
                {days.map( day => (
                    <Day 
                        key={day} 
                        day={day} 
                        values={getDayValues(day)}
                        setValues={getSetDayValues(day)}
                        addRow={addRow}
                        removeRow={removeRow}
                    />
                ))}
            </ScheduleTable>
            <TokenExpirate modal={tokenExpirate} admin/>
            {err  && <Message>{message}</Message>}
            {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
            <EditBtn onClick={handleBtn} margin="1vw 0 0 0">GUARDAR</EditBtn>
        </ScheduleTableContainer>
    )
}

export default Schedule
