import React, { useState, useEffect, useRef } from 'react'
import Cookies from 'js-cookie';
import axios from 'axios'

import TopBar from '../components/home/TopBar'
import Navbar from '../components/Navbar'
import Info from '../components/home/Info'
import Services from '../components/home/Services'
import LoginForm from '../components/home/LoginForm'
import CarouselImages from '../components/home/CarouselImages'
import ModalEdit from '../components/admin/ModalEdit'

import { MainContainer } from '../styles/Home'

import { URL as URL_SERVER} from '../variables'

function Home({ adminEdit }) {

    const homeRef = useRef(null)

    const [imgs, setImgs] = useState([])
    const [deletedImgs, setDeletedImgs] = useState([])
    const [uploadImgs, setUploadImgs] = useState([])
    const [bgNavbar, setBgNavbar] = useState(false)
    const [isLogged, setIsLogged] = useState(false)
    const [modal, setModal] = useState(false)

    const handleScroll = () => {
        if(homeRef.current.scrollTop > window.innerHeight * 0.05){ setBgNavbar(true) }
        else{ setBgNavbar(false) }
    }

    const getIMages = async () => {
        const res = await axios({
            url: `${URL_SERVER}/info?images=true`,
            method: 'GET'
        })

        return res.data
    }

    const postImages = async (data) => {
        const res = await axios({
            url: `${URL_SERVER}/admin/imgs`,
            method: 'PUT',
            data
        })

        return res.data
    }

    const handleUploadImages = () => {
        if(adminEdit){ setModal(!modal) }
    }

    const closeModal = () => setModal(false)

    const removeImg = idx => {
        let newImgs = [...imgs]
        URL.revokeObjectURL(newImgs[idx].url)
        if(newImgs[idx].id){
            let newDeletedImgs = [...deletedImgs]
            newDeletedImgs.push(newImgs[idx])
            setDeletedImgs(newDeletedImgs)
        }else{
            let newUploadImgs = [...uploadImgs]
            const nIdx = newUploadImgs.findIndex(item => item.idx === idx)
            newUploadImgs.splice(nIdx, 1)
            setUploadImgs(newUploadImgs)
        }
        newImgs.splice(idx, 1)
        setImgs(newImgs)
    }

    const addImage = img => {
        let newImgs = [...imgs]
        let newUploadImgs = [...uploadImgs]
        newImgs.push({ url: URL.createObjectURL(img) })
        newUploadImgs.push({ idx: newImgs.length - 1, img })
        setImgs(newImgs)
        setUploadImgs(newUploadImgs)
    }
    
    useEffect(() => {
        homeRef.current.addEventListener('scroll', handleScroll)
        if(Cookies.get('payload')){ setIsLogged(true) }
        getIMages().then(({ images }) => {
            setImgs(images)
        }).catch(e => {
            console.log(e)
        })
        return () => {
            homeRef.current.removeEventListener('scroll', handleScroll)
        }
    }, [])

    return (
        <MainContainer ref={homeRef} height={adminEdit ? '29%' : '100%'}>
            {!adminEdit && (
                <>
                    <TopBar/>
                    <Navbar bgNavbar={bgNavbar} isLogged={isLogged}/>
                </>
            )}
            <CarouselImages 
                imgs={imgs} 
                adminEdit={adminEdit} 
                handleUploadImages={handleUploadImages}
            />
            <Info />
            <Services />
            <LoginForm isLogged={isLogged} adminEdit={adminEdit}/>
            <ModalEdit 
                modal={modal} 
                closeModal={closeModal}
                imgs={imgs}
                removeItem={removeImg}
                addImage={addImage}
                saveImages={postImages}
                deletedImgs={deletedImgs}
                uploadImgs={uploadImgs}
            />
        </MainContainer>
    )
}

export default Home
