import React from 'react'
import Modal from 'react-modal';

import { TokenExpirateModal } from '../styles/Admin' 

Modal.setAppElement('#root')

const customStyles = {
    content : {
        top: '35%',
        left: '40%',
        right: 'auto',
        bottom: 'auto',
        width: '20%',
        height: '30%',
        borderRadius: '0',
        border: '0',
    }
};

function TokenExpirate({ modal, admin }) {

    const gotoLogin = () => {
        if(admin){ window.location.replace('/admin/login') }
        else{ window.location.replace('/#login') }
    }

    return (
        <Modal
            isOpen={modal}
            onRequestClose={gotoLogin}
            style={customStyles}
            contentLabel="Example Modal"
        >
            <TokenExpirateModal>
                <h1>Su sesión ha expirado. Vuelva a iniciar sesión</h1>
                <button onClick={gotoLogin}>INICIAR SESIÓN</button>
            </TokenExpirateModal>
        </Modal>
    )
}

export default TokenExpirate
