import React, { useEffect, useState, useRef } from 'react'
import axios from 'axios'

import { FormContainer, Label } from '../../styles/Payment'
import {  Message, EditBtn } from '../../styles/User'

import { URL } from '../../variables'

function Form({ location }) {

    const refYear = useRef(null)

    let doSubmit = false

    const [email, setEmail] = useState('')
    const [name, setName] = useState('')
    const [description, setDescription] = useState('')
    const [message, setMessage] = useState('')
    const [auth, setAuth] = useState(false)
    const [err, setErr] = useState(false)
    const [success, setSuccess] = useState(false)
    const [total, setTotal] = useState(150)

    const getInfo = async (token) => {
        const res = await axios({
            url: `${URL}/payment?token=${token}`,
            method: 'GET'
        })

        return res.data
    }

    const paymentSuccess = async (dateID, token) => {
        const res = await axios({
            url: `${URL}/payment?token=${token}`,
            method: 'POST',
            data: { dateID }
        })

        return res.data
    }

    const setData = (date, type) => {
        if(date.length === 0){
            setMessage('Ya se ha pagado esta cita.')
            setAuth(false)
            return setSuccess(true)
        }
        setEmail(date[0].email)
        setName(date[0].nombre)
        if(type === "medico"){ setDescription(`Cita con el ${date[0].cargo} ${date[0].nombre}`) }
        else{
            let description = 'Cita para'
            for(const service of date){
                description = `${description} ${service.nombre},`
            }
            setDescription(description)
        }
    }

    const addBtnPayment = preferenceID => {
        const script = document.createElement('script')
        script.src = "https://www.mercadopago.com.mx/integrations/v1/web-payment-checkout.js"
        script.setAttribute('data-preference-id', preferenceID)
        document.getElementById('payment-form').appendChild(script)
    }

    const checkQueries = (token, dateID, status) => {
        if(dateID && status === "success" && token){
            paymentSuccess(dateID, token).then(({ }) => {
                setSuccess(true)
                setMessage('Se ha confirmado el pago')
            }).catch(e => {
                setErr(true)
                setMessage('No se ha podido confirmar su pago. Notifique al médico que ya realizó su pago si se retiró el dinero de su tarjeta.')
            })
        }else if(token){
            getInfo(token).then(({ date, type, preferenceID }) => {
                setAuth(true)
                addBtnPayment(preferenceID)
                setData(date, type)
            }).catch(e => {
                setErr(true)
                if(e.response?.status === 401){ return setMessage('El token no es valido o ha caducado.') }
                setMessage('Ha ocurrido un error intentando obtener la información del pago. Intentelo más tarde.')
            })
        }else{
            setErr(true)
            setMessage('No deberías estar aquí.')
        }
    }

    const gotoHome = () => window.location.replace('/')

    useEffect(() => {
        const query = new URLSearchParams(location.search);
        const token = query.get('token')
        const dateID = query.get('dateID')
        const status = query.get('status')
        checkQueries(token, dateID, status)
        
    }, [])

    return (
        auth ? (
        <>
        <FormContainer id="payment-form">
            <div>
                <Label htmlFor="name">Nombre: </Label>
                <input id="name" name="name" type="text" value={name}/>
            </div>
            <div>
                <Label htmlFor="email">E-mail: </Label>
                <input id="email" name="email" type="text" value={email} disabled/>
            </div>
            <div className="textarea-container">
                <Label htmlFor="description">Descripción: </Label>
                <textarea id="description" name="description" type="text" value={description} disabled/>
            </div>
            <div>
                <Label>Total: ${total}</Label>
            </div>
        </FormContainer>
        </>
        ): (err || success) && (
            <>
            <Message color={success ? 'rgb(0,47,91)' : 'red'}>{message}</Message>
            {success && <EditBtn onClick={gotoHome} margin="1vw 0 0 0">VOLVER AL INICIO</EditBtn>}
            </>
        )
        
    )
}

export default Form
