import React from 'react'

import { MenuContainer } from '../../styles/User'

function Menu({ logout, setView }) {

    const handleProfile = () => setView('profile')
    const handleEdit = () => setView('edit')
    const handleHistory = () => setView('history')

    return (
        <MenuContainer>
            <h2>Menú</h2>
            <ul>
                <li onClick={handleProfile}>Perfil</li>
                <li onClick={handleEdit}>Editar</li>
                <li onClick={handleHistory}>Citas</li>
                <li onClick={logout}>Cerrar sesión</li>
            </ul>
        </MenuContainer>
    )
}

export default Menu
