import React, { useState } from 'react'

import TokenExpirate from '../TokenExpirate'
import Schedule from './Schedule'

import { ContentContainer, TitleContainer, ErrMessage } from '../../styles/Date'

function View() {

    const [err, setErr] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [message, setMessage] = useState('')

    return (
        <ContentContainer pTop="0">
            <TitleContainer><h1>HORARIO</h1></TitleContainer>
            <Schedule />
            {err && <ErrMessage color="red" width="100%">{message}</ErrMessage>}
            <TokenExpirate modal={tokenExpirate} admin/>
        </ContentContainer>
    )
}

export default View
