import React, { useState, useEffect } from 'react'
import Modal from 'react-modal';
import Loader from 'react-loader-spinner'
import Select from 'react-select';
import axios from 'axios';

import { LoginTitle, LoginFormLabel, LoginInput, LoginFormBtn, LoginMessage } from '../../styles/Home'
import { CreateUserContainer } from '../../styles/Admin'
import { EditBtn } from '../../styles/User'

import { URL } from '../../variables'

Modal.setAppElement('#root')

const customStyles = {
    content : {
        top: '20%',
        left: '40%',
        right: 'auto',
        bottom: 'auto',
        width: '20%',
        height: '60%',
        borderRadius: '0',
        border: '0',
        backgroundColor: 'rgb(235, 235, 235)'
    }
};

const options = [{ value: 'MEDICO', label: 'Médico' },{ value: 'LAB', label: 'Laboratorista' },{ value: 'ASISTENTE', label: 'Asistente' }]
const optionsDoctor = [{ value: 'MEDICO', label: 'Médico' },{ value: 'NUTRIOLOGO', label: 'Nutriólogo' }]

function CreateUser({ modal, closeModal, setSessionExpirate }) {

    const [email, setEmail] = useState('')
    const [speciality, setspeciality] = useState('')
    const [message, setMessage] = useState('')
    const [loading, setLoading] = useState(false)
    const [err, setErr] = useState(false)
    const [success, setsuccess] = useState(false)
    const [labs, setLabs] = useState([])
    const [doctors, setDoctors] = useState([])
    const [lab, setLab] = useState({})
    const [doctor, setDoctor] = useState({})
    const [userType, setUserType] = useState(options[0])
    const [doctorType, setDoctorType] = useState(optionsDoctor[0])

    const handleEmail = e => setEmail(e.target.value)
    const handleSpeciality = e => setspeciality(e.target.value)
    const handleSelect = value => setUserType(value)
    const handleSelectDoctor = value => setDoctorType(value)
    const handleSelectLab = value => setLab(value)
    const handleSelectAssistant = value => setDoctor(value)

    const checkEmail = () => {
        const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
    }

    const createUser = async (data) => {
        const res = await axios({
            url: `${URL}/admin/user`,
            method: 'POST',
            data
        })

        return res.data
    }

    const getLabs = async () => {
        const res = await axios({
            url: `${URL}/all-labs`,
            method: 'GET'
        })

        return res.data
    }

    const getDoctors = async () => {
        const res = await axios({
            url: `${URL}/all-medicos`,
            method: 'GET'
        })

        return res.data
    }

    const checkData = () => {
        if(!checkEmail()){ 
            setErr(true)
            setMessage('Introduce un email valido.')
            return false 
        }if(speciality === ""){
            setErr(true)
            setMessage('El campo especialidad no puede esttar vacio.')
            return false 
        }
        return true
    }

    const handleBtn = e => {
        e.preventDefault()
        if(checkData()){
            setErr(false)
            setLoading(true)
            let data = { email, userType, speciality }
            if(userType.value === "MEDICO"){ data.doctorType = doctorType }
            else if(userType.value === "LAB"){ data.labType = lab }
            else{ data.asisType = doctor }
            createUser(data).then(() => {
                setLoading(false)
                setsuccess(true)
                setMessage('Se ha creado el usuario correctamente.')
            }).catch(e => {
                console.log(e)
                setLoading(false)
                if(e.response?.status === 401){ return setSessionExpirate(true) }
                setErr(true)
                setMessage('Ha ocurrido un error intentando crear el usuario. Intentalo más tarde o reporta el problema.')
            })
        }
        
    }

    useEffect(() => {
        getLabs().then(({ labs }) => {
            const labsOptions = labs.map(lab => { return { value: lab.id, label: lab.nombre } })
            setLabs(labsOptions)
            setLab(labsOptions[0])
        }).catch(e => {
            console.log(e)
            if(e.response?.status === 401){ return setSessionExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido un error intentando obtener la información necesaria para crear un usuario. Intentalo más tarde o reporta el problema.')
        })
        getDoctors().then(({ doctors }) => {
            const doctorOptions = doctors.map(lab => { return { value: lab.id, label: lab.nombre } })
            setDoctors(doctorOptions)
            setDoctor(doctorOptions[0])
        }).catch(e => {
            console.log(e)
            if(e.response?.status === 401){ return setSessionExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido un error intentando obtener la información necesaria para crear un usuario. Intentalo más tarde o reporta el problema.')
        })
    }, [])

    return (
        <Modal
            isOpen={modal}
            onRequestClose={closeModal}
            style={customStyles}
            contentLabel="Example Modal"
        >
            <CreateUserContainer >
                <LoginTitle>CREAR USUARIO</LoginTitle>
                <LoginFormLabel htmlFor="email">EMAIL</LoginFormLabel>
                <LoginInput 
                    onChange={handleEmail}
                    value={email}
                    type="email"
                    id="email"
                />
                <LoginFormLabel html="speciality">ESPECIALIDAD</LoginFormLabel>
                <LoginInput 
                    onChange={handleSpeciality}
                    value={speciality}
                    type="text"
                    id="speciality"
                />
                <LoginFormLabel >TIPO DE USUARIO</LoginFormLabel>
                <Select
                    value={userType}
                    onChange={handleSelect}
                    options={options}
                    className="select-user"
                    classNamePrefix="select"
                    placeholder="Tipo de usuario"
                />
                {userType.value === "MEDICO" && (
                    <>
                        <LoginFormLabel>TIPO DE MÉDICO</LoginFormLabel>
                        <Select
                            value={doctorType}
                            onChange={handleSelectDoctor}
                            options={optionsDoctor}
                            className="select-user"
                            classNamePrefix="select"
                            placeholder="Tipo de médico"
                        />
                    </>
                )}
                {userType.value === "LAB" && (
                    <>
                        <LoginFormLabel>LABORATORIO</LoginFormLabel>
                        <Select
                            value={lab}
                            onChange={handleSelectLab}
                            options={labs}
                            className="select-user"
                            classNamePrefix="select"
                            placeholder="Tipo de médico"
                        />
                    </>
                )}
                {userType.value === "ASISTENTE" && (
                    <>
                        <LoginFormLabel>DOCTOR</LoginFormLabel>
                        <Select
                            value={doctor}
                            onChange={handleSelectAssistant}
                            options={doctors}
                            className="select-user"
                            classNamePrefix="select"
                            placeholder="Tipo de médico"
                        />
                    </>
                )}
                <EditBtn onClick={handleBtn} margin="1vw 0 1vw 0">ACEPTAR</EditBtn>
                
                {(err || success) && <LoginMessage color={success  ? 'rgb(0,47,91)' : 'red'}>{message}</LoginMessage>}
                {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
            </CreateUserContainer>
        </Modal>
    )
}

export default CreateUser
