import React, { useEffect, useState } from 'react'
import Cookies from 'js-cookie';
import axios from 'axios';

import MainContainer from '../components/MainContainer'
import Menu from '../components/user/Menu'
import Info from '../components/user/Info'
import History from '../components/user/History'
import Form from '../components/user/Form'

import { Container } from '../styles/User'

import { URL } from '../variables'

function User() {

    const [edit, setEdit] = useState(false)
    const [profile, setProfile] = useState(true)
    const [history, setHistory] = useState(false)
    const [user, setUser] = useState({})

    const getUser = async () => {
        const res = await axios({
            url: `${URL}/client`,
            method: 'GET'
        })

        return res.data
    }

    const fetchLogout = async () => {
        const res = await axios({
            url: `${URL}/client/logout`,
            method: 'GET'
        })

        return res.data
    }

    const logout = () => {
        fetchLogout().then(() => {
            window.location.replace('/')
        }).catch(e => {
            console.log(e)
            window.location.reload()
        })
    }

    const setView = (view) => {
        setEdit(view === "edit")
        setProfile(view === "profile")
        setHistory(view === "history")
    }

    const cancelEdit = () => {
        setEdit(false)
        setProfile(true)
    }

    useEffect(() => {
        if(!Cookies.get('payload')){ window.location.replace('/') }
        getUser().then(({user}) => {
            setUser(user)
        }).catch((e) => {
            console.log(e)
        })
    }, [])

    return (
        <MainContainer title="USUARIO">
            <Container>
                <Menu 
                    setView={setView}
                    logout={logout}
                />
                {edit && <Form user={user} cancelEdit={cancelEdit}/>}
                {profile && <Info user={user} />}
                {history && <History/>}
            </Container>
        </MainContainer>
    )
}

export default User
