import React from 'react'

import Review from './Review'
import PostReview from './PostReview'

import { ReviewsContainer, ReviewsGroup, ReviewContainer, ReviewText, ReviewUser } from '../../styles/Info'

function Reviews({ reviews, itemID, infoType, isLogged }) {

    console.log()

    return (
        <ReviewsContainer>
            <ReviewsGroup>
                {reviews.length !== 0 ? (
                    reviews.filter(item => item.texto).map( (review, idx) => (
                        <Review 
                            key={review.id}  
                            review={review}
                            idx={idx}
                        />
                    ) )
                ):(
                    <ReviewContainer bgColor='rgb(246,246,246)'>
                        <div className="review">
                            <ReviewText>
                                <ReviewUser>TODAVÍA NO SE HA HECHO UNA RESEÑA</ReviewUser>
                            </ReviewText>
                        </div>
                    </ReviewContainer>
                )}
            </ReviewsGroup>
            {isLogged && <PostReview id={itemID} infoType={infoType}/>}
        </ReviewsContainer>
    )
}

export default Reviews
