import React from 'react'
import TimePicker from 'rc-time-picker';


import { ScheduleDayContainer } from '../../styles/Doctor'
import { BtnCircle, BtnContainer } from '../../styles/User'

function Day({ day, values, setValues, addRow, removeRow, toShow, width, idx }) {

    const handleBtnAdd = () => addRow(day)
    const handleBtnRemove = () => removeRow(day)

    const handlePickerStart = (vPicker, idx) => {
        let newValues = [...values]
        newValues[idx].start = vPicker
        setValues(newValues)
    }

    const handlePickerEnd = (vPicker, idx) => {
        let newValues = [...values]
        newValues[idx].end = vPicker
        setValues(newValues)
    }

    return (
        <ScheduleDayContainer width={width} bgColor={idx % 2 === 0 ? 'rgb(235,235,235)' : 'rgb(245,245,245)'}>
            <div className="header">{day}</div>
            {values?.map((value, idx) => (
                toShow ? (
                    <div className="row" key={idx}>
                        <span>{value.inicio}</span>
                        <span className="span-to-show">-</span>
                        <span>{value.final}</span>
                    </div>
                ):(
                    <div className="row" key={idx}>
                        <TimePicker
                            value={value.start}
                            onChange={(vPicker) => {handlePickerStart(vPicker, idx)}}
                            showSecond={false}
                            minuteStep={30}
                            className="picker"
                        />
                        <span>-</span>
                        <TimePicker
                            value={value.end}
                            onChange={(vPicker) => {handlePickerEnd(vPicker, idx)}}
                            showSecond={false}
                            minuteStep={30}
                            className="picker"
                        />
                    </div>
                )))}
            {!toShow && (
                <BtnContainer>
                    <BtnCircle onClick={handleBtnRemove} mLeft="0">-</BtnCircle>
                    <BtnCircle onClick={handleBtnAdd} mLeft="0">+</BtnCircle>
                </BtnContainer>
            )}
        </ScheduleDayContainer>
    )
}

export default Day
