import React, { useState } from 'react'
import Loader from 'react-loader-spinner'
import axios from 'axios'

import { DateItemContainer, DateItemInfo, LoaderContainer } from '../../styles/User'

import { URL } from '../../variables'

function DateItem({ date, idx, setTokenExpirate }) {

    if(date.servicio){ console.log(date) }

    const [open, setOpen] = useState(false)
    const [loading, setLoading] = useState(false)
    const [recipes, setRecipes] = useState([])

    const twoDigits = number => number < 10 ? `0${number}` : number

    const getHour = () => `${twoDigits(new Date(date.fecha).getHours())}:${twoDigits(new Date(date.fecha).getMinutes())}`

    const getDate = () => {
        const nDate = new Date(date.fecha)
        return `${twoDigits(nDate.getDate())}/${twoDigits(nDate.getMonth() + 1)}/${nDate.getFullYear()} ${getHour()}`
    }

    const handleClick = () => {
        setOpen(!open)
        if(!open){
            setLoading(true)
            getRecipe().then(({ recipe }) => {
                setLoading(false)
                setRecipes(recipe.map(recipeDB => recipeDB.receta))
            }).catch(e => {
                console.log(e)
                setLoading(false)
                if(e.response.status === 401){ return setTokenExpirate(true) }
            })
        }
    }

    const getService = () => {
        if(date.medico){ return `${date.nombre} ${date.cargo}` }
        else{ return `${date.servnombre}` }
    }

    const getRecipe = async () => {
        const res = await axios({
            url: `${URL}/cita/${date.id}`,
            method: 'GET'
        })

        return res.data
    }
    

    return (
        <DateItemContainer
            height={date.medico ? open ? loading ? `${(Math.ceil(date.padecimiento.length / 75) * 1.1) + 6}vw` : 
                                        `${(Math.ceil(date.padecimiento.length / 75) * 1.1) + 5 + recipes.length * 1.1}vw` : 
                                    '1vw' : 
                    '1vw'}
            bgColor={idx %2 === 0 ? 'rgb(245, 245, 245)' : 'rgb(235, 235, 235)'}
            onClick={handleClick}
        >
            <div className="column">
                <DateItemInfo width="100%">
                    <DateItemInfo width="20%">
                        <span className="bold">Fecha:&nbsp;</span>
                        <span> {getDate()}</span>
                    </DateItemInfo>
                    <DateItemInfo width={date.medico ? '48%' : '33%'}>
                        <span className="bold">{date.medico ? 'Doctor' : 'Laboratorio'}:&nbsp;</span>
                        <span>{getService()}</span>
                    </DateItemInfo>
                    {date.servicio && (
                        <DateItemInfo width="15%">
                            <span className="bold">Domicilio:&nbsp;</span>
                            <span>{date.direccion ? 'Si' : 'No'}</span>
                        </DateItemInfo>
                    )}
                    <DateItemInfo width="15%">
                            <span className="bold">Pagado:&nbsp;</span>
                            <span>{date.pagado ? 'Si' : 'No'}</span>
                        </DateItemInfo>
                    <DateItemInfo width="17%">
                        <span className="bold">Estado:&nbsp;</span>
                        <span>{date.aprobado ? 'Aprobado' : 'Sin aprobar'}</span>
                    </DateItemInfo>
                </DateItemInfo>
                {date.medico && (
                    <>
                    <DateItemInfo width="100%" mTop="1vw">
                        {date.medico && (
                            <DateItemInfo width="15%">
                                <span className="bold">Domicilio:&nbsp;</span>
                                <span>{date.direccion ? 'Si' : 'No'}</span>
                            </DateItemInfo>
                        )}
                        <DateItemInfo width="85%">
                            <span className="bold">Padecimiento:&nbsp;</span>
                            <span>{date.padecimiento}</span>
                        </DateItemInfo>
                    </DateItemInfo>
                    <DateItemInfo width="100%" mTop="1vw">
                        <span className="bold">Receta:&nbsp;</span>
                        <span>{recipes.length === 0 && 'Todavía no hay una receta'}</span>
                    </DateItemInfo>
                    {recipes.map((recipe, idx) => (
                        <DateItemInfo width="100%" key={idx}>
                            <span className="bold">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                            <span>{recipe}</span>
                        </DateItemInfo>
                    ))}
                    </>
                )}
                {loading && (
                    <LoaderContainer>
                        <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>
                    </LoaderContainer>
                )}
            </div>
        </DateItemContainer>
    )
}

export default DateItem
