import React from 'react'

import { FormContainer, InputContainer, InputLabel, Input, TextArea, CheckboxInputContainer } from '../../styles/Date'
import { Checkbox, LabsServiceText } from '../../styles/Labs'

function Form({ name, setName, age, setAge, illness, setIllness, phone, setPhone, email, setEmail, address, setAddress, homeService, setHomeService, dateType }) {

    const handleName = e => setName(e.target.value)
    const handleAge = e => setAge(e.target.value.replace(/[^0-9]/g, ''))
    const handleIllness = e => setIllness(e.target.value)
    const handlePhone = e => setPhone(e.target.value.replace(/[^0-9]/g, ''))
    const handleEmail = e => setEmail(e.target.value)
    const handleAddress = e => setAddress(e.target.value)

    const handleCheckbox = () => setHomeService(!homeService)

    return (
        <FormContainer>
            <InputContainer>
                <InputLabel htmlFor="nombre">Nombre:</InputLabel>
                <Input 
                    id="nombre"
                    value={name}
                    onChange={handleName}
                />
            </InputContainer>
            <InputContainer>
                <InputLabel htmlFor="edad">Edad:</InputLabel>
                <Input 
                    id="edad"
                    value={age}
                    onChange={handleAge}
                    type=""
                />
            </InputContainer>
            {dateType === "medico" && (
                <InputContainer align="flex-start">
                    <InputLabel htmlFor="padecimiento">Padecimiento:</InputLabel> 
                    <TextArea 
                        id="padecimiento"
                        value={illness}
                        onChange={handleIllness}
                    />
                </InputContainer>
            )}
            <InputContainer>
                <InputLabel htmlFor="telefono">Teléfono:</InputLabel>
                <Input 
                    id="telefono"
                    value={phone}
                    onChange={handlePhone}
                />
            </InputContainer>
            <InputContainer>
                <InputLabel htmlFor="email">Email:</InputLabel>
                <Input 
                    id="email"
                    value={email}
                    onChange={handleEmail}
                    type="email"
                />
            </InputContainer>
            <InputContainer mgBottom="0">
                <InputLabel htmlFor="address">Dirección:</InputLabel>
                <CheckboxInputContainer>
                    <input 
                        id="address"
                        value={address}
                        onChange={handleAddress}
                        disabled={!homeService}
                        bgColor={homeService ? null : 'rgb(180,180,180)'}
                    /> 
                    <div className="checkbox-container">
                        <Checkbox onClick={handleCheckbox} bgColor={!homeService ? 'rgb(255,255,255)': 'rgb(0,47,91)'}>
                        </Checkbox>
                        <LabsServiceText width="fit-content">Servicio a domicilio</LabsServiceText>
                    </div>
                </CheckboxInputContainer>
            </InputContainer>
        </FormContainer>
    )
}

export default Form
