import React, { useEffect, useState } from 'react'
import axios from 'axios'

import TokenExpirate from '../TokenExpirate'

import { DiscountAnnouncement, DiscounntText, DiscounntTextBold, LineSeparator, DiscounntInputBold, DiscounntInput, LoginFormBtn, LoginMessage } from '../../styles/Home'

import { URL }  from '../../variables'

function PromInfo({ width, sizeTBold, sizeTNormal, lineWidth, adminEdit }) {

    const [text1, setText1] = useState('')
    const [text2, setText2] = useState('')
    const [text3, setText3] = useState('')
    const [message, setMessage] = useState('')
    const [ID, setID] = useState(0)
    const [textChanged, setTextChanged] = useState(false)
    const [err, setErr] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)

    const handleText1 = e => {
        setText1(e.target.value)
        setTextChanged(true)
    }
    const handleText2 = e => {
        setText2(e.target.value)
        setTextChanged(true)
    }
    const handleText3 = e => {
        setText3(e.target.value)
        setTextChanged(true)
    }

    const getInfo = async () => {
        const res = await axios({
            url: `${URL}/info?disccountInfo=true`,
            method: 'GET'
        })

        return res.data
    }

    const saveInfo = async () => {
        const res = await axios({
            url: `${URL}/admin/text`,
            method: 'PUT',
            data: { text1, text2, text3, ID }
        })

        return res.data
    }

    const handleBtn = () => {
        if(checkData()){
            saveInfo().then(() => {
                window.location.reload()
            }).catch(e => {
                console.log(e)
                setErr(true)
                if(e.reponse?.status === 400){ return setMessage('No se puede dejar los textos en blanco') }
                if(e.reponse?.status === 401){ return setTokenExpirate(true) }
                setMessage('Ha ocurrido un error intentando actualizar el texto.\nIntentalo más tarde o reporta el problema.')
            })
        }
    }

    const checkData = () => {
        if(text1 === "" || text2 === "" || text3 === "") {
            setErr(true)
            setMessage('No se puede dejar los textos en blanco')
            return false
        }
        return true
    }

    useEffect(() => {
        getInfo().then(({ texto1, texto2, texto3, id }) => {
            setText1(texto1)
            setText2(texto2)
            setText3(texto3)
            setID(id)
        }).catch((e) => {
            console.log(e)
            setText1('DESCUENTOS')
            setText2('EN SERVICIOS SELECCIONADOS')
            setText3('PAGANDO AQUÍ')
        })
    }, [])

    return (
        <DiscountAnnouncement
            width={width}
        >
            {adminEdit ? (
                <>
                    <DiscounntInputBold 
                        color="rgb(178,223,252)" size={`${sizeTBold}vw`}
                        value={text1} onChange={handleText1}
                    />
                    <DiscounntInput 
                        size={sizeTNormal}
                        value={text2} onChange={handleText2}
                    />
                    <DiscounntInputBold 
                        size={`${sizeTBold+1}vw`}
                        value={text3} onChange={handleText3}
                    />
                </>
            ):(
                <>
                <DiscounntTextBold color="rgb(178,223,252)" size={`${sizeTBold}vw`}>{text1}</DiscounntTextBold>
                <DiscounntText size={sizeTNormal}>{text2}</DiscounntText>
                <DiscounntTextBold size={`${sizeTBold+1}vw`}>{text3}</DiscounntTextBold>
                </>
            )}
            <LineSeparator
                color="rgb(178,223,252)"
                height="0.7vw"
                lineWidth={lineWidth}
            />
            <TokenExpirate admin modal={tokenExpirate}/>
            {textChanged && <LoginFormBtn bgColor="rgb(235, 235, 235)" color="rgb(0,47,91)" width="fit-content" onClick={handleBtn}>GUARDAR</LoginFormBtn>}
            {err && <LoginMessage mTop="1vw">{message}</LoginMessage>}
        </DiscountAnnouncement>
    )
}

export default PromInfo
