import React, { useState, useRef } from 'react'
import Select from 'react-select';
import axios from 'axios';

import { FilterContainer, FilterDateSearch } from '../../styles/Doctor'

import { URL } from '../../variables'

const options = [
    { value: 'date', label: 'Fecha' },
    { value: 'patient', label: 'Paciente' },
];

function Filter({ setDates, cancelFilter, setSessionExpirate, setMessageErr, changeOrder }) {

    const refInputMonth = useRef(null)
    const refInputYear = useRef(null)

    const [typeFilter, setTypeFilter] = useState(options[0])
    const [day, setDay] = useState('')
    const [month, setMonth] = useState('')
    const [year, setYear] = useState('')
    const [patient, setPatient] = useState('')

    const handleSelect = value => {
        setTypeFilter(value)
        if(value.value !== typeFilter.value){ changeOrder(value.value) }
    }

    const handleInputDay = e => {
        let text = e.target.value.replace(/[^0-9]/g, '')
        if(text.length <= 2 && Number(text) < 32){ setDay(text) }
        if(text.length === 2){ refInputMonth.current.focus() }
    }

    const handleInpuMonth = e => {
        let text = e.target.value.replace(/[^0-9]/g, '')
        if(text.length <= 2 && Number(text) < 13){ setMonth(text) }
        if(text.length === 2){ refInputYear.current.focus() }
    }

    const handleInputYear = e => {
        if(e.target.value.replace(/[^0-9]/g, '').length <= 4){ setYear(e.target.value.replace(/[^0-9]/g, '')) }
    }

    const handleInputPatient = e => setPatient(e.target.value)

    const getDates = async date => {
        const res = await axios({
            url: `${URL}/cita/filter?${typeFilter.value}=${typeFilter.value === "date" ? date : patient}`,
            method: 'GET'
        })

        return res.data
    }

    const handleButton = e => {
        e.preventDefault()
        if((typeFilter.value === "date" && (day === "" || month === "" || year === "")) || (typeFilter.value === "patient" && patient === "")){
            setDay('')
            setMonth('')
            setYear('')
            setPatient('')
            return cancelFilter(typeFilter.value) 
        }

        let date = new Date()
        date.setDate(Number(day))
        date.setMonth(Number(month) -1)
        date.setFullYear(Number(year))

        getDates(date.getTime()).then(({ dates, query }) => {
            console.log(dates)
            setDates(dates, typeFilter.value, query)
        }).catch(e => {
            console.log(e)
            if(e.response.status === 401){ return setSessionExpirate(true) }
            setMessageErr('Ha ocurrido un error en el servidor, intentalo otra vez o reporta el problema.')
        })
    }

    return (
        <FilterContainer>
            <div className="select-container">
                <label>Ordenar por</label>
                <Select
                    value={typeFilter}
                    onChange={handleSelect}
                    options={options}
                    className="select-signup"
                    classNamePrefix="select"
                    placeholder="Ordenar por"
                />
            </div>
            {typeFilter.value === "date" ? (
                <FilterDateSearch onSubmit={handleButton}>
                    <label>Buscar fecha</label>
                    <input 
                        value={day}
                        onChange={handleInputDay}
                    />
                    <span>/</span>
                    <input 
                        value={month}
                        onChange={handleInpuMonth}
                        ref={refInputMonth}
                    />
                    <span>/</span>
                    <input 
                        value={year}
                        onChange={handleInputYear}
                        ref={refInputYear}
                    />
                    <button onClick={handleButton}>BUSCAR</button>
                </FilterDateSearch>
            ):(
                <FilterDateSearch onSubmit={handleButton}>
                    <label>Buscar paciente</label>
                    <input 
                        value={patient}
                        onChange={handleInputPatient}
                        className="input-patient"
                        placeholder="Nombre del paciente"
                    />
                    <button onClick={handleButton}>BUSCAR</button>
                </FilterDateSearch>
            )}
        </FilterContainer>
    )
}

export default Filter
