import React, { useState } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import { BtnFinishDate, ErrMessage } from '../../styles/Date'
import { InputContainer, MessageSuccess } from '../../styles/SignUp'

import { URL } from '../../variables'

function BtnSignUp({ name, age, address, phone, email, illness, relativeIllness, emergencyContact, allergies, bloodType, password, confirmPassword, rh }) {
    
    const [err, setErr] = useState(false)
    const [success, setSuccess] = useState(false)
    const [loading, setLoading] = useState(false)
    const [message, setMessage] = useState('')

    const createUser = async (data) => {
        const res = await axios({
            url: `${URL}/client`,
            method: 'POST',
            data
        })

        return res.data
    }

    const handleBtn = () => {
        if(checkData() && !loading){
            setLoading(true)
            setSuccess(false)
            setErr(false)
            const data = { name, age, address, phone, email, illness, relativeIllness, emergencyContact, allergies, bloodType: bloodType.value, password, rh: rh.value }
            createUser(data).then( ({ ok, message }) => {
                setLoading(false)
                if(!ok){
                    setErr(true)
                    setMessage(message)
                    return
                }
                setSuccess(true)
            }).catch((e) => {
                setLoading(false)
                if(e.response.status === 400){
                    setMessage('DEBES COMPLETAR TODOS LOS CAMPOS')
                    setErr(true)
                    return
                }
                setMessage('HA OCURRIDO UN ERROR EN EL SERVIDOR. VUELVE A INTENTARLO MÁS TARDE')
                setErr(true)
                setSuccess(false)
            })
        }
    }

    const checkData = () => {
        if(name === "" || age === "" || address === "" || phone === "" || email === "" || illness === "" || relativeIllness === "" ||
        emergencyContact === "" || allergies === "" || password === "" || confirmPassword === "" || rh === "" || bloodType === ""){
            setMessage('Debes de completar todos los campos')
            setErr(true)
            return false
        }if(password !== confirmPassword){
            setMessage('Las contraseñas deben coincidir')
            setErr(true)
            return false
        }if(!checkAge()){
            setMessage('Tienes que ser mayor de edad para crearte una cuenta')
            setErr(true)
            return false
        }if(!checkPhone()){
            setMessage('Los números telefónicos deben de ser validos')
            setErr(true)
            return false
        }if(!checkEmail()){
            setMessage('El email debe de ser valido')
            setErr(true)
            return false
        }
        setMessage('')
        setErr(false)
        return true
    }

    const checkAge = () => age > 17 && age < 110
    const checkPhone = () => phone.length === 10 && emergencyContact.length === 10
    const checkEmail = () => {
        const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
    }

    return ( 
        <InputContainer>
            {(err || success ||loading) ? (
                err ? <ErrMessage color='red' width="60%" mTop="0">{message}</ErrMessage> :
                success ? <MessageSuccess>SE HA ENVIADO UN CORREO DE CONFIRMACIÓN A TU EMAIL.</MessageSuccess> :
                <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>
            ) : <div></div>}
            <BtnFinishDate onClick={handleBtn}>CREAR CUENTA</BtnFinishDate>
        </InputContainer>
    )
}

export default BtnSignUp
