import React from 'react'
import Select from 'react-select';

import { InputContainer, InputForm, TextareaForm } from '../../styles/SignUp'
import { InputLabel } from '../../styles/Date'

const options = [
    { value: 'A', label: 'A' },
    { value: 'B', label: 'B' },
    { value: 'AB', label: 'AB' },
    { value: 'O', label: 'O' }
];

const optionsRH = [ { value: 'Negativo', label: 'Negativo' }, { value: 'Positivo', label: 'Positivo' }];

function Input({ label, value, setValue, id, number, textarea, type, height, widthLabel, value1, setValue1 }) {

    const handleInput = (e) => {
        if(number){ setValue(e.target.value.replace(/[^0-9]/g, '')) }
        else{ setValue(e.target.value) }
    }

    return (
        <InputContainer>
            <InputLabel htmlFor={id} width={widthLabel}>{label}</InputLabel>
            {id === "blood" ? (
                <>
                <Select
                    value={value}
                    onChange={setValue}
                    options={options}
                    className="select-signup"
                    classNamePrefix="select"
                    placeholder="Tipo de sangre"
                />
                <Select
                    value={value1}
                    onChange={setValue1}
                    options={optionsRH}
                    className="select-signup"
                    classNamePrefix="select"
                    placeholder="RH"
                />
                </>
            ): (
                textarea ? (
                    <TextareaForm 
                        value={value}
                        onChange={handleInput}
                        id={id}
                        height={height}
                    />
                ):(
                    <InputForm 
                        value={value}
                        onChange={handleInput}
                        id={id}
                        type={type || 'text'}
                    />
                )
            )}
        </InputContainer>
    )
}

export default Input
