import React from 'react'

import { Checkbox, LabsServiceCheckboxContainer } from '../../styles/Labs'
import { EditBtn } from '../../styles/User'

function MenuUsers({ handleCheckboxDoctors, handleCheckboxLabs, handleCheckboxAssistant, userType, createUser }) {
    return (
        <ul className="menu">
            <li>
                <LabsServiceCheckboxContainer 
                    pLef="1vw"
                    mRight="1vw"
                >
                    <Checkbox onClick={handleCheckboxDoctors} bgColor={userType !== "MEDICO" ? 'white': 'rgb(0,47,91)'}>
                    </Checkbox>
                    <p>Médicos</p>
                </LabsServiceCheckboxContainer>
            </li>
            <li>
                <LabsServiceCheckboxContainer 
                    pLef="1vw"
                    mRight="1vw"
                >
                    <Checkbox onClick={handleCheckboxLabs} bgColor={userType !== "LAB" ? 'white': 'rgb(0,47,91)'}>
                    </Checkbox>
                    <p>Laboratoristas</p>
                </LabsServiceCheckboxContainer>
            </li>
            <li>
                <LabsServiceCheckboxContainer 
                    pLef="1vw"
                    mRight="1vw"
                >
                    <Checkbox onClick={handleCheckboxAssistant} bgColor={userType !== "ASISTENTE" ? 'white': 'rgb(0,47,91)'}>
                    </Checkbox>
                    <p>Asistentes</p>
                </LabsServiceCheckboxContainer>
            </li>
            <li className="btn">
                <EditBtn margin="0" onClick={createUser}>CREAR USUARIO</EditBtn>
            </li>
        </ul>
    )
}

export default MenuUsers
