import styled from 'styled-components'

//const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightBlue = "rgb(178,223,252)"

export const MenuContainer = styled.div`
    width: ${props => props.width};
    height: 100vh;
    background-color: rgb(240, 240, 240);
    display: flex;
    flex-direction: column;
    transition: width 0.5s ease-in-out;
    overflow-x: hidden;
    flex-shrink: 0;
    position: fixed;
    left: 0;
    top: 0;
`

export const MenuTitle = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: ${props => props.bgColor};
    transition: background-color 0.5s ease-in-out;
    padding: 2vh 1vw;

    div{ 
        min-width: 3vw;
        font-size: 2vw;
    }

    svg{ &:hover{ cursor: pointer; } }

    h1{
        font-family: Montserrat;
        color: ${darkBlue};
        font-size: 2vw;
        margin: 0;
    }
`

export const MenuContent = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 5vh;

    div{
        display: flex;
        flex-direction: row;
        align-items: center;
        background-color: white;
        border-radius: 0.5rem;
        padding: 0.2vh 1vw;        
        opacity: ${props => props.opacity};
        transition: opacity 0.5s ease-in-out;
        margin-bottom: 2vw;
        min-width: 11.5vw;

        &:hover{ cursor: pointer; }

        h2{
            color: ${darkBlue};
            font-family: Montserrat;
            font-size: 1.2vw;
            margin-left: 1vw;
        }

        svg{
            fill: ${darkBlue};
        }
    }
`

export const TitleContainer = styled.div`
    width: 100%;
    padding: 1vw 0;
`

export const ContentContainer = styled.div`
    width: ${props => props.width};
    flex-direction: column;
    display: flex;
    padding: 0 1vw;
    flex-shrink: 1;
    transition: width 0.5s ease-in-out;
`

export const MainContainer = styled.div`
    width: ${props => props.width};
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: ${props => props.overflowHidden ? 'hidden' : 'auto'}; 
    display: flex;
    flex-direction: row;
    height: ${props => props.height};
    padding-left: ${props => props.pLeft};
    transition: all 0.5s ease-in-out;
`

export const Content = styled.div`
    width: ${props => props.width || '100%'};
    height: ${props => props.height || 'fit-content'};
    display: flex;
    align-items: ${props => props.alignItems }; 
    justify-content: ${props => props.justifyContent }; 
    flex-direction: ${props => props.flexDirection};
    margin: ${props => props.margin || '0'};
`

export const MessageError = styled.p`
    color: red;
    font-family: MontserratBold;
    font-size: 1.5vw;
    text-align: center;
`

export const TokenExpirateModal = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h1{
        font-family: MontserratBold;
        color: ${darkBlue};
        font-size: 1.5vw;
        text-align: center;
    }

    button{
        background-color: ${darkBlue};
        font-family: MontserratMedium;
        font-size: 1vw;
        color: white;
        padding: 0.5vw 1vw;
        border-radius: 0.3rem;
        border: 0;
        margin-left: 1vw;

        &:hover{ cursor: pointer; }
    }
`

export const AdminEditBtnContainer = styled.div`
    width: 100%;
    height: 5vh;
    position: relative;
    top: ${props => props.top || '-7vh'};
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-bottom: -5vh;
`

export const ModalImagesContainer = styled.div`
    width: 90%;
    max-height: 30vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 5%;
    overflow-y: auto;

    div{
        margin-top: 1vw;
        display: flex;
        flex-direction: row;
        justify-content: center;

        img{
            width: 12vw;
            height: 12vh;
            background-size: contain;
            background-repeat: no-repeat;
        }

        button{
            width: 1vw;
            height: 1vw;
            background-color: rgb(235, 235, 235);
            border-radius: 0.5vw;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            position: relative;
            left: -1.5vw;
            top: 0.5vw;
            font-size: 0.6vw;

            &:focus{ outline: none; }
        }

    }

    
`

export const AddImageBtn = styled.div`
    margin-top: 1vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(235, 235, 235);
    width: 12vw;
    height: 12vh;
    cursor: pointer;
    font-size: 3vw;
    margin-right: 0.5vw;
`

export const BtnModalContainer = styled.div`
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`

export const EmptyWarning = styled.div`
    width: 80%;
    padding: 1vw 0;
    font-family: Montserrat;
    font-size: 1vw;
    background-color: rgb(235, 235, 235);
    display: flex;
    align-items: center;
    justify-content: center;
`

export const UsersContainer = styled.div`
    width: 76vw;
    padding: 0 2vw;
    display: flex;
    flex-direction: row;

    .menu{
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        width: 15%;

        li{
            list-style: none;
            background-color: rgb(235, 235, 235);
            text-align: center;
            padding: 0 1vw;
        }

        .btn{
            background-color: white;
            padding: 0;
            margin-top: 2vw;
        }
    }

    .content{
        width: 81%;
        padding: 0 2%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
    }

`

export const CreateUserContainer = styled.form`
    width: 90%;
    padding: 0 5%;
    display: flex;
    align-items: center;
    flex-direction: column;

    .select-user{
        margin-bottom: 1vw;
    }
`